import React, { useState, useEffect } from "react";
import { Button, CircularProgress, Switch, FormControlLabel } from "@material-ui/core";
import { PlayArrow, Stop, Schedule } from "@material-ui/icons";
import toast from "react-hot-toast";
import { Select, CaretIcon, ModalCloseButton } from "react-responsive-select";
import { readSettings } from "../../services/settings";
import {
    startClock,
    stopClock,
    getClockStatus,
    updateClockSetting,
} from "../../services/clock";
import { dispatchTickerStarted, onTickerStarted } from "../../services/tickerEvents";

type ClockSettings = {
    clockFace: string;
    clockUse12Hour: boolean;
    clockShowSeconds: boolean;
};

// Clock face options
const clockFaceOptions = [
    { text: "Cyberpunk Neon", value: "cyberpunk" },
    { text: "Synthwave", value: "synthwave" },
    { text: "Tron Grid", value: "tron" },
    { text: "Digital Waves", value: "waves" },
    { text: "Word Clock", value: "word" },
    { text: "Linear Timeline", value: "linear" },
    { text: "Swatch .Beats", value: "swatch" },
    { text: "7-Segment LED", value: "segment" },
];


interface ClockWidgetProps {
    onClockStart?: () => void;
}

const ClockWidget: React.FC<ClockWidgetProps> = ({ onClockStart }) => {
    const [settings, setSettings] = useState<ClockSettings>({
        clockFace: "cyberpunk",
        clockUse12Hour: true,
        clockShowSeconds: true,
    });
    const [isClockRunning, setIsClockRunning] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [settingsLoaded, setSettingsLoaded] = useState<boolean>(false);

    // Load settings on mount
    useEffect(() => {
        readSettings()
            .then((data: any) => {
                if (data) {
                    setSettings({
                        clockFace: data.clockFace || "cyberpunk",
                        clockUse12Hour: data.clockUse12Hour !== false,
                        clockShowSeconds: data.clockShowSeconds !== false,
                    });
                    setSettingsLoaded(true);
                }
            })
            .catch(console.error);

        // Check clock status
        getClockStatus()
            .then((data: any) => {
                setIsClockRunning(data.running || false);
            })
            .catch(() => setIsClockRunning(false));

        // Listen for other tickers starting - reset our button state
        const unsubscribe = onTickerStarted((tickerType) => {
            if (tickerType !== 'clock') {
                setIsClockRunning(false);
            }
        });

        return () => unsubscribe();
    }, []);

    const handleStartClock = async () => {
        setIsLoading(true);

        try {
            await startClock(
                settings.clockFace,
                settings.clockUse12Hour,
                settings.clockShowSeconds
            );
            setIsClockRunning(true);
            setIsLoading(false);
            dispatchTickerStarted('clock');
            onClockStart?.();
            toast.success("Clock Started!");
        } catch (error) {
            setIsLoading(false);
            toast.error("Failed to start clock");
        }
    };

    const handleStopClock = () => {
        toast.promise(stopClock(), {
            loading: "Stopping Clock...",
            success: () => {
                setIsClockRunning(false);
                return "Clock Stopped";
            },
            error: "Failed to stop clock",
        });
    };

    const updateSetting = (key: string, value: string) => {
        return updateClockSetting(key, value);
    };

    // Handle clock face change
    const handleFaceChange = async (changes: any) => {
        const face = changes.value;
        const newSettings = { ...settings, clockFace: face };
        setSettings(newSettings);
        updateSetting("clockFace", face);

        // Auto-restart if running
        if (isClockRunning) {
            try {
                await startClock(
                    face,
                    newSettings.clockUse12Hour,
                    newSettings.clockShowSeconds
                );
            } catch (error) {
                console.error("Failed to restart clock", error);
            }
        }
    };

    // Handle 12-hour toggle
    const handleUse12HourChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
        const use12Hour = event.target.checked;
        const newSettings = { ...settings, clockUse12Hour: use12Hour };
        setSettings(newSettings);
        updateSetting("clockUse12Hour", use12Hour ? "yes" : "no");

        // Auto-restart if running
        if (isClockRunning) {
            try {
                await startClock(
                    newSettings.clockFace,
                    use12Hour,
                    newSettings.clockShowSeconds
                );
            } catch (error) {
                console.error("Failed to restart clock", error);
            }
        }
    };

    // Handle show seconds toggle
    const handleShowSecondsChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
        const showSeconds = event.target.checked;
        const newSettings = { ...settings, clockShowSeconds: showSeconds };
        setSettings(newSettings);
        updateSetting("clockShowSeconds", showSeconds ? "yes" : "no");

        // Auto-restart if running
        if (isClockRunning) {
            try {
                await startClock(
                    newSettings.clockFace,
                    newSettings.clockUse12Hour,
                    showSeconds
                );
            } catch (error) {
                console.error("Failed to restart clock", error);
            }
        }
    };

    return (
        <div className="widget-section">
            <h3 className="section-title">
                <Schedule style={{ marginRight: 8, verticalAlign: "middle" }} />
                Clock Settings
            </h3>

            {/* Clock Face Selection */}
            <div className="setting-row">
                <label className="setting-label">Clock Face</label>
                <Select
                    key={`clock-face-${settingsLoaded}-${settings.clockFace}`}
                    selectedValue={settings.clockFace}
                    name="clockFace"
                    options={clockFaceOptions}
                    caretIcon={<CaretIcon />}
                    modalCloseButton={<ModalCloseButton />}
                    onChange={handleFaceChange}
                />
            </div>

            {/* Settings Grid */}
            <div className="settings-grid" style={{ gridTemplateColumns: "repeat(2, 1fr)" }}>
                {/* Time Format Toggle */}
                <div className="setting-item">
                    <label className="setting-label">Time Format</label>
                    <FormControlLabel
                        control={
                            <Switch
                                checked={settings.clockUse12Hour}
                                onChange={handleUse12HourChange}
                                color="primary"
                            />
                        }
                        label={settings.clockUse12Hour ? "12-Hour" : "24-Hour"}
                        style={{ color: "#fff", marginLeft: 0 }}
                    />
                </div>

                {/* Seconds Display Toggle */}
                <div className="setting-item">
                    <label className="setting-label">Seconds Display</label>
                    <FormControlLabel
                        control={
                            <Switch
                                checked={settings.clockShowSeconds}
                                onChange={handleShowSecondsChange}
                                color="primary"
                            />
                        }
                        label={settings.clockShowSeconds ? "Show Seconds" : "Hide Seconds"}
                        style={{ color: "#fff", marginLeft: 0 }}
                    />
                </div>
            </div>

            {/* Start Button */}
            <Button
                variant="contained"
                onClick={isClockRunning ? handleStopClock : handleStartClock}
                disabled={isLoading}
                className={`start-button ${isClockRunning ? "stop" : ""}`}
            >
                {isLoading ? (
                    <CircularProgress size={24} style={{ color: "#fff", marginRight: 8 }} />
                ) : isClockRunning ? (
                    <Stop />
                ) : (
                    <PlayArrow />
                )}
                {isLoading ? "PLEASE WAIT..." : isClockRunning ? "STOP CLOCK" : "START CLOCK"}
            </Button>
        </div>
    );
};

export default ClockWidget;
