import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import PowerOff from "@material-ui/icons/PowerSettingsNew";
import Refresh from "@material-ui/icons/Refresh";
import SystemUpdateAlt from "@material-ui/icons/SystemUpdateAlt";
import { rebootSystem, shutdownSystem, checkForUpdates } from "../../services";
import toast from "react-hot-toast";
import { Settings, Widgets, Collections, TextFields, Wallpaper, Assignment, CloudUpload } from "@material-ui/icons";
import "./styles.css";

const MainMenu: React.FC = () => {
    const nav = useNavigate();
    const [updateAvailable, setUpdateAvailable] = useState<boolean>(false);
    const [checkingUpdate, setCheckingUpdate] = useState<boolean>(true);
    const [checkFailed, setCheckFailed] = useState<boolean>(false);

    useEffect(() => {
        checkForUpdates()
            .then((info) => {
                setUpdateAvailable(info?.updateAvailable || false);
                setCheckFailed(false);
            })
            .catch(() => {
                setUpdateAvailable(false);
                setCheckFailed(true); // USB-only or no internet
            })
            .finally(() => {
                setCheckingUpdate(false);
            });
    }, []);

    return (
        <div className="fade-in main-menu">
            <div className="menu-grid">
                <div
                    className="menu-card"
                    onClick={() => nav(`/consoles`)}
                >
                    <Collections className="menu-icon" />
                    <span className="menu-label">Browse Marquees</span>
                </div>
                <div
                    className="menu-card"
                    onClick={() => nav(`/widgets`)}
                >
                    <Widgets className="menu-icon" />
                    <span className="menu-label">Widgets</span>
                </div>
                <div
                    className="menu-card"
                    onClick={() => nav(`/default-marquee`)}
                >
                    <Wallpaper className="menu-icon" />
                    <span className="menu-label">Default Marquee</span>
                </div>
                <div
                    className="menu-card"
                    onClick={() => nav(`/upload-artwork`)}
                >
                    <CloudUpload className="menu-icon" />
                    <span className="menu-label">Upload Artwork</span>
                </div>
                <div
                    className="menu-card"
                    onClick={() => nav(`/text`)}
                >
                    <TextFields className="menu-icon" />
                    <span className="menu-label">Text</span>
                </div>
                <div
                    className="menu-card"
                    onClick={() => nav(`/settings`)}
                >
                    <Settings className="menu-icon" />
                    <span className="menu-label">Settings</span>
                </div>
                <div
                    className="menu-card"
                    onClick={() => nav(`/log`)}
                >
                    <Assignment className="menu-icon" />
                    <span className="menu-label">Log</span>
                </div>
                <div
                    className={`menu-card ${updateAvailable ? 'update-available' : !checkingUpdate && !checkFailed ? 'up-to-date' : ''}`}
                    onClick={() => nav(`/update`)}
                >
                    <SystemUpdateAlt className="menu-icon" />
                    <span className="menu-label">
                        {checkingUpdate ? 'Checking...' : updateAvailable ? 'Update Available!' : checkFailed ? 'Updates' : 'Up to Date'}
                    </span>
                    {updateAvailable && <span className="update-badge-dot"></span>}
                    {!checkingUpdate && !updateAvailable && !checkFailed && <span className="up-to-date-check"></span>}
                </div>
                <div
                    className="menu-card system-card"
                    onClick={(e) => {
                        // Show submenu or handle inline
                        const target = e.currentTarget;
                        const submenu = target.querySelector('.system-submenu');
                        if (submenu) {
                            submenu.classList.toggle('visible');
                        }
                    }}
                >
                    <PowerOff className="menu-icon" />
                    <span className="menu-label">System</span>
                    <div className="system-submenu">
                        <button
                            className="submenu-btn"
                            onClick={(e) => {
                                e.stopPropagation();
                                toast.promise(shutdownSystem(), {
                                    loading: "Attempting Shutdown",
                                    success: "Shutdown Succeeded",
                                    error: "Shutdown Failed",
                                });
                            }}
                        >
                            <PowerOff style={{ fontSize: 18, marginRight: 6 }} />
                            Shutdown
                        </button>
                        <button
                            className="submenu-btn"
                            onClick={(e) => {
                                e.stopPropagation();
                                toast.promise(rebootSystem(), {
                                    loading: "Attempting to Reboot",
                                    success: "Reboot Succeeded",
                                    error: "Reboot Failed",
                                });
                            }}
                        >
                            <Refresh style={{ fontSize: 18, marginRight: 6 }} />
                            Reboot
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default MainMenu;
