import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { Button, CircularProgress } from "@material-ui/core";
import {
    SportsBasketball,
    TrendingUp,
    WbSunny,
    RssFeed,
    Schedule,
    Settings,
    PlayArrow,
    Stop,
} from "@material-ui/icons";
import toast from "react-hot-toast";
import { readSettings } from "../../services/settings";
import { startSportsTicker, stopSportsTicker, getSportsTickerStatus, startFavoritesTicker } from "../../services/sports";
import { getDeviceUrl } from "../../services";
import axios from "axios";
import { dispatchTickerStarted, onTickerStarted, TickerType } from "../../services/tickerEvents";
import "./styles.css";

type TickerStatus = {
    sports: boolean;
    stocks: boolean;
    weather: boolean;
    news: boolean;
    clock: boolean;
};

type LoadingState = {
    sports: boolean;
    stocks: boolean;
    weather: boolean;
    news: boolean;
    clock: boolean;
};

const WidgetsMenu: React.FC = () => {
    const nav = useNavigate();
    const [tickerStatus, setTickerStatus] = useState<TickerStatus>({
        sports: false,
        stocks: false,
        weather: false,
        news: false,
        clock: false,
    });
    const [loading, setLoading] = useState<LoadingState>({
        sports: false,
        stocks: false,
        weather: false,
        news: false,
        clock: false,
    });
    const [settings, setSettings] = useState<any>(null);

    // Fetch all ticker statuses on mount
    useEffect(() => {
        const fetchStatuses = async () => {
            try {
                // Load settings first
                const settingsData = await readSettings();
                setSettings(settingsData);

                // Check sports status
                const sportsStatus = await getSportsTickerStatus();

                // Check stocks status
                const stocksRes = await axios.get(`${getDeviceUrl("")}/stocks/status`);

                // Check weather status
                const weatherRes = await axios.get(`${getDeviceUrl("")}/weather/status`);

                // Check news status
                const newsRes = await axios.get(`${getDeviceUrl("")}/news/status`);

                // Check clock status
                const clockRes = await axios.get(`${getDeviceUrl("")}/clock/status`);

                setTickerStatus({
                    sports: sportsStatus?.running || false,
                    stocks: stocksRes.data?.running || false,
                    weather: weatherRes.data?.running || false,
                    news: newsRes.data?.running || false,
                    clock: clockRes.data?.running || false,
                });
            } catch (err) {
                console.error("Error fetching ticker statuses:", err);
            }
        };

        fetchStatuses();

        // Listen for ticker started events from other components
        const unsubscribe = onTickerStarted((tickerType: TickerType) => {
            // Reset all to false, then set the started one to true
            setTickerStatus({
                sports: tickerType === 'sports',
                stocks: tickerType === 'stocks',
                weather: tickerType === 'weather',
                news: tickerType === 'news',
                clock: tickerType === 'clock',
            });
        });

        return () => unsubscribe();
    }, []);

    // Generic ticker start/stop handlers
    const handleSportsTicker = async () => {
        if (tickerStatus.sports) {
            // Stop
            try {
                await stopSportsTicker();
                setTickerStatus(prev => ({ ...prev, sports: false }));
                toast.success("Sports Ticker Stopped");
            } catch (err) {
                toast.error("Failed to stop Sports Ticker");
            }
        } else {
            // Start with saved settings
            setLoading(prev => ({ ...prev, sports: true }));
            try {
                const mode = settings?.sportTickerMode || "league";
                const refreshMinutes = (settings?.sportTickerRefresh || 60) / 60;
                const speed = settings?.sportScrollSpeed || 5;

                if (mode === "favorites" && settings?.sportFavoriteTeams) {
                    await startFavoritesTicker(settings.sportFavoriteTeams, refreshMinutes, speed);
                } else if (settings?.sportLeagues) {
                    const teams = settings?.sportTeamCodes ? settings.sportTeamCodes.split(",") : [];
                    await startSportsTicker(settings.sportLeagues, teams, refreshMinutes, speed);
                } else {
                    toast.error("Please configure Sports settings first");
                    setLoading(prev => ({ ...prev, sports: false }));
                    return;
                }
                setTickerStatus({ sports: true, stocks: false, weather: false, news: false, clock: false });
                dispatchTickerStarted('sports');
                toast.success("Sports Ticker Started!");
            } catch (err) {
                toast.error("Failed to start Sports Ticker");
            } finally {
                setLoading(prev => ({ ...prev, sports: false }));
            }
        }
    };

    const handleStocksTicker = async () => {
        if (tickerStatus.stocks) {
            try {
                await axios.get(`${getDeviceUrl("")}/stocks/stop`);
                setTickerStatus(prev => ({ ...prev, stocks: false }));
                toast.success("Stocks Ticker Stopped");
            } catch (err) {
                toast.error("Failed to stop Stocks Ticker");
            }
        } else {
            setLoading(prev => ({ ...prev, stocks: true }));
            try {
                const symbols = settings?.stockSymbols || "MSFT,GOOG,AMZN";
                const refresh = settings?.stockTickerRefresh || 5;
                const speed = settings?.stockScrollSpeed || 5;
                const font = settings?.stockFont || "LiberationSans-Bold";
                await axios.get(`${getDeviceUrl("")}/stocks/start?symbols=${encodeURIComponent(symbols)}&refresh=${refresh}&speed=${speed}&font=${encodeURIComponent(font)}`);
                setTickerStatus({ sports: false, stocks: true, weather: false, news: false, clock: false });
                dispatchTickerStarted('stocks');
                toast.success("Stocks Ticker Started!");
            } catch (err) {
                toast.error("Failed to start Stocks Ticker");
            } finally {
                setLoading(prev => ({ ...prev, stocks: false }));
            }
        }
    };

    const handleWeatherWidget = async () => {
        if (tickerStatus.weather) {
            try {
                await axios.get(`${getDeviceUrl("")}/weather/stop`);
                setTickerStatus(prev => ({ ...prev, weather: false }));
                toast.success("Weather Widget Stopped");
            } catch (err) {
                toast.error("Failed to stop Weather Widget");
            }
        } else {
            setLoading(prev => ({ ...prev, weather: true }));
            try {
                const location = settings?.weatherLocation || "95050";
                const units = settings?.weatherUnits || "fahrenheit";
                const forecast = settings?.weatherForecast || "1day";
                await axios.get(`${getDeviceUrl("")}/weather/start?location=${encodeURIComponent(location)}&units=${units}&forecast=${forecast}`);
                setTickerStatus({ sports: false, stocks: false, weather: true, news: false, clock: false });
                dispatchTickerStarted('weather');
                toast.success("Weather Widget Started!");
            } catch (err) {
                toast.error("Failed to start Weather Widget");
            } finally {
                setLoading(prev => ({ ...prev, weather: false }));
            }
        }
    };

    const handleNewsTicker = async () => {
        if (tickerStatus.news) {
            try {
                await axios.get(`${getDeviceUrl("")}/news/stop`);
                setTickerStatus(prev => ({ ...prev, news: false }));
                toast.success("News Ticker Stopped");
            } catch (err) {
                toast.error("Failed to stop News Ticker");
            }
        } else {
            setLoading(prev => ({ ...prev, news: true }));
            try {
                const sources = settings?.newsSources || "bbc";
                const refresh = settings?.newsTickerRefresh || 5;
                const speed = settings?.newsScrollSpeed || 5;
                await axios.get(`${getDeviceUrl("")}/news/start?sources=${encodeURIComponent(sources)}&refresh=${refresh}&speed=${speed}`);
                setTickerStatus({ sports: false, stocks: false, weather: false, news: true, clock: false });
                dispatchTickerStarted('news');
                toast.success("News Ticker Started!");
            } catch (err) {
                toast.error("Failed to start News Ticker");
            } finally {
                setLoading(prev => ({ ...prev, news: false }));
            }
        }
    };

    const handleClockWidget = async () => {
        if (tickerStatus.clock) {
            try {
                await axios.get(`${getDeviceUrl("")}/clock/stop`);
                setTickerStatus(prev => ({ ...prev, clock: false }));
                toast.success("Clock Widget Stopped");
            } catch (err) {
                toast.error("Failed to stop Clock Widget");
            }
        } else {
            setLoading(prev => ({ ...prev, clock: true }));
            try {
                const face = settings?.clockFace || "cyberpunk";
                const use12Hour = settings?.clockUse12Hour === "yes";
                const showSeconds = settings?.clockShowSeconds === "yes";
                await axios.get(`${getDeviceUrl("")}/clock/start?face=${face}&use12Hour=${use12Hour}&showSeconds=${showSeconds}`);
                setTickerStatus({ sports: false, stocks: false, weather: false, news: false, clock: true });
                dispatchTickerStarted('clock');
                toast.success("Clock Widget Started!");
            } catch (err) {
                toast.error("Failed to start Clock Widget");
            } finally {
                setLoading(prev => ({ ...prev, clock: false }));
            }
        }
    };

    return (
        <div className="widgets-menu fade-in">
            <div className="widgets-grid">
                {/* Sports Ticker */}
                <Button
                    variant="contained"
                    className={`widget-button ${tickerStatus.sports ? 'active' : ''}`}
                    onClick={handleSportsTicker}
                    disabled={loading.sports}
                >
                    {loading.sports ? (
                        <CircularProgress size={24} style={{ color: "#fff" }} />
                    ) : tickerStatus.sports ? (
                        <Stop className="widget-icon" />
                    ) : (
                        <SportsBasketball className="widget-icon" />
                    )}
                    <span className="widget-label">
                        {tickerStatus.sports ? "Stop Sports" : "Sports Scoreboard"}
                    </span>
                </Button>

                {/* Stocks Ticker */}
                <Button
                    variant="contained"
                    className={`widget-button ${tickerStatus.stocks ? 'active' : ''}`}
                    onClick={handleStocksTicker}
                    disabled={loading.stocks}
                >
                    {loading.stocks ? (
                        <CircularProgress size={24} style={{ color: "#fff" }} />
                    ) : tickerStatus.stocks ? (
                        <Stop className="widget-icon" />
                    ) : (
                        <TrendingUp className="widget-icon" />
                    )}
                    <span className="widget-label">
                        {tickerStatus.stocks ? "Stop Stocks" : "Stocks & Crypto"}
                    </span>
                </Button>

                {/* Weather Widget */}
                <Button
                    variant="contained"
                    className={`widget-button ${tickerStatus.weather ? 'active' : ''}`}
                    onClick={handleWeatherWidget}
                    disabled={loading.weather}
                >
                    {loading.weather ? (
                        <CircularProgress size={24} style={{ color: "#fff" }} />
                    ) : tickerStatus.weather ? (
                        <Stop className="widget-icon" />
                    ) : (
                        <WbSunny className="widget-icon" />
                    )}
                    <span className="widget-label">
                        {tickerStatus.weather ? "Stop Weather" : "Weather"}
                    </span>
                </Button>

                {/* News Ticker */}
                <Button
                    variant="contained"
                    className={`widget-button ${tickerStatus.news ? 'active' : ''}`}
                    onClick={handleNewsTicker}
                    disabled={loading.news}
                >
                    {loading.news ? (
                        <CircularProgress size={24} style={{ color: "#fff" }} />
                    ) : tickerStatus.news ? (
                        <Stop className="widget-icon" />
                    ) : (
                        <RssFeed className="widget-icon" />
                    )}
                    <span className="widget-label">
                        {tickerStatus.news ? "Stop News" : "News"}
                    </span>
                </Button>

                {/* Clock Widget */}
                <Button
                    variant="contained"
                    className={`widget-button ${tickerStatus.clock ? 'active' : ''}`}
                    onClick={handleClockWidget}
                    disabled={loading.clock}
                >
                    {loading.clock ? (
                        <CircularProgress size={24} style={{ color: "#fff" }} />
                    ) : tickerStatus.clock ? (
                        <Stop className="widget-icon" />
                    ) : (
                        <Schedule className="widget-icon" />
                    )}
                    <span className="widget-label">
                        {tickerStatus.clock ? "Stop Clock" : "Clock"}
                    </span>
                </Button>

                {/* Widget Settings */}
                <Button
                    variant="contained"
                    className="widget-button settings-button"
                    onClick={() => nav('/widget-settings')}
                >
                    <Settings className="widget-icon" />
                    <span className="widget-label">Widget Settings</span>
                </Button>
            </div>
        </div>
    );
};

export default WidgetsMenu;
