import axios from "axios";
import { getDeviceUrl } from ".";

// Start the weather widget
export const startWeatherWidget = (
    location: string,
    units: string = "fahrenheit",
    showUnits: boolean = true,
    forecast: string = "1day",
    refresh: number = 1800
): Promise<any> => {
    const params = new URLSearchParams({
        location: location,
        units: units,
        showUnits: showUnits ? "yes" : "no",
        forecast: forecast,
        refresh: refresh.toString(),
    });
    const requestUrl = `${getDeviceUrl("")}/weather/start?${params.toString()}`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Stop the weather widget
export const stopWeatherWidget = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/weather/stop`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Get weather widget status
export const getWeatherStatus = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/weather/status`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Fetch weather data without starting the widget
export const fetchWeatherData = (location: string): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/weather/fetch?location=${encodeURIComponent(location)}`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Update a weather setting
export const updateWeatherSetting = (key: string, value: string): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/settings?key=${key}&value=${encodeURIComponent(value)}`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};
