import React, { useState, useEffect } from "react";
import { Button, CircularProgress } from "@material-ui/core";
import { Close, PlayArrow, RssFeed, Stop } from "@material-ui/icons";
import toast from "react-hot-toast";
import { Select, CaretIcon, ModalCloseButton } from "react-responsive-select";
import { FontList } from "../../const/fonts";
import { readSettings } from "../../services/settings";
import {
    startNewsTicker,
    stopNewsTicker,
    getNewsTickerStatus,
    updateNewsSetting,
} from "../../services/news";
import { dispatchTickerStarted, onTickerStarted } from "../../services/tickerEvents";

type NewsSettings = {
    newsSources: string[]; // Array of source keys
    newsCustomURL: string;
    newsTickerRefresh: number;
    newsTickerColor: string;
    newsTickerFont: string;
    newsScrollSpeed: number;
};

// News source categories and options
const newsSourceCategories = {
    "World News": [
        { text: "BBC News", value: "bbc" },
        { text: "Reuters", value: "reuters" },
        { text: "AP News", value: "ap" },
        { text: "NPR News", value: "npr" },
        { text: "The Guardian", value: "guardian" },
    ],
    "US News": [
        { text: "CNN", value: "cnn" },
        { text: "Fox News", value: "foxnews" },
        { text: "NY Times", value: "nytimes" },
        { text: "Washington Post", value: "washpost" },
        { text: "USA Today", value: "usatoday" },
    ],
    "Tech News": [
        { text: "Hacker News", value: "hackernews" },
        { text: "TechCrunch", value: "techcrunch" },
        { text: "The Verge", value: "theverge" },
        { text: "Ars Technica", value: "arstechnica" },
        { text: "Wired", value: "wired" },
    ],
    "Gaming News": [
        { text: "IGN", value: "ign" },
        { text: "Kotaku", value: "kotaku" },
        { text: "Polygon", value: "polygon" },
        { text: "GameSpot", value: "gamespot" },
    ],
    "Finance News": [
        { text: "MarketWatch", value: "marketwatch" },
        { text: "CNBC", value: "cnbc" },
        { text: "Bloomberg", value: "bloomberg" },
    ],
    "Custom": [
        { text: "Custom RSS Feed", value: "custom" },
    ],
};

// Flatten for dropdown - only show sources not already selected
const getAvailableSourceOptions = (selectedSources: string[]) => {
    const options: { text: string; value: string }[] = [];
    Object.entries(newsSourceCategories).forEach(([category, sources]) => {
        sources.forEach((source) => {
            if (!selectedSources.includes(source.value)) {
                options.push({
                    text: `${source.text}`,
                    value: source.value,
                });
            }
        });
    });
    return options;
};

// Get source display name
const getSourceName = (sourceKey: string): string => {
    for (const sources of Object.values(newsSourceCategories)) {
        const source = sources.find((s) => s.value === sourceKey);
        if (source) return source.text;
    }
    return sourceKey;
};

// Color presets
const colorPresets = [
    { name: "White", value: "FFFFFF" },
    { name: "Red", value: "FF0000" },
    { name: "Green", value: "00FF00" },
    { name: "Blue", value: "0000FF" },
    { name: "Yellow", value: "FFFF00" },
    { name: "Cyan", value: "00FFFF" },
    { name: "Magenta", value: "FF00FF" },
    { name: "Orange", value: "FFA500" },
];

// Speed presets
const speedPresets = [
    { text: "Slow", value: "2" },
    { text: "Normal", value: "5" },
    { text: "Fast", value: "10" },
    { text: "Very Fast", value: "15" },
];

// Refresh presets (in minutes)
const refreshPresets = [
    { text: "1 minute", value: "1" },
    { text: "5 minutes", value: "5" },
    { text: "15 minutes", value: "15" },
    { text: "30 minutes", value: "30" },
];

interface NewsWidgetProps {
    onTickerStart?: () => void;
}

const NewsWidget: React.FC<NewsWidgetProps> = ({ onTickerStart }) => {
    const [settings, setSettings] = useState<NewsSettings>({
        newsSources: ["bbc"],
        newsCustomURL: "",
        newsTickerRefresh: 5,
        newsTickerColor: "FFFFFF",
        newsTickerFont: "LiberationSans-Regular",
        newsScrollSpeed: 5,
    });
    const [isTickerRunning, setIsTickerRunning] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [customURLInput, setCustomURLInput] = useState("");
    const [settingsLoaded, setSettingsLoaded] = useState<boolean>(false);

    // Load settings on mount
    useEffect(() => {
        readSettings()
            .then((data: any) => {
                if (data) {
                    // Parse newsSources from comma-separated string
                    const sourcesStr = data.newsSources || "bbc";
                    const sourcesArray = sourcesStr.split(",").map((s: string) => s.trim()).filter((s: string) => s);
                    const newsCustomURL = data.newsCustomURL || "";
                    setSettings({
                        newsSources: sourcesArray.length > 0 ? sourcesArray : ["bbc"],
                        newsCustomURL,
                        newsTickerRefresh: data.newsTickerRefresh || 5,
                        newsTickerColor: data.newsTickerColor || "FFFFFF",
                        newsTickerFont: data.newsTickerFont || "LiberationSans-Regular",
                        newsScrollSpeed: data.newsScrollSpeed || 5,
                    });
                    setCustomURLInput(newsCustomURL);
                    setSettingsLoaded(true);
                }
            })
            .catch(console.error);

        // Check ticker status
        getNewsTickerStatus()
            .then((data: any) => {
                setIsTickerRunning(data.running || false);
            })
            .catch(() => setIsTickerRunning(false));

        // Listen for other tickers starting - reset our button state
        const unsubscribe = onTickerStarted((tickerType) => {
            if (tickerType !== 'news') {
                setIsTickerRunning(false);
            }
        });

        return () => unsubscribe();
    }, []);

    // Get font options for dropdown
    const getFontOptions = () => {
        return FontList.map((font) => ({
            value: font,
            text: font,
        }));
    };

    // Convert sources array to comma-separated string
    const sourcesToString = (sources: string[]): string => {
        return sources.join(",");
    };

    // Restart ticker with current settings
    const restartTicker = async (newSettings: NewsSettings) => {
        try {
            const customURL = newSettings.newsSources.includes("custom") ? newSettings.newsCustomURL : "";
            await startNewsTicker(
                sourcesToString(newSettings.newsSources),
                customURL,
                newSettings.newsScrollSpeed,
                60,
                newSettings.newsTickerRefresh,
                newSettings.newsTickerColor,
                newSettings.newsTickerFont
            );
            setIsTickerRunning(true);
            dispatchTickerStarted('news');
        } catch (error) {
            console.error("Failed to restart ticker", error);
        }
    };

    const handleStartTicker = async () => {
        if (settings.newsSources.length === 0) {
            toast.error("Please add at least one news source");
            return;
        }

        if (settings.newsSources.includes("custom") && !customURLInput.trim()) {
            toast.error("Please enter a custom RSS feed URL");
            return;
        }

        const customURL = settings.newsSources.includes("custom") ? customURLInput.trim() : "";

        setIsLoading(true);

        try {
            await startNewsTicker(
                sourcesToString(settings.newsSources),
                customURL,
                settings.newsScrollSpeed,
                60,
                settings.newsTickerRefresh,
                settings.newsTickerColor,
                settings.newsTickerFont
            );
            setIsTickerRunning(true);
            setIsLoading(false);
            dispatchTickerStarted('news');
            onTickerStart?.();
            toast.success("News Ticker Started!");
        } catch (error) {
            setIsLoading(false);
            toast.error("Failed to start ticker");
        }
    };

    const handleStopTicker = () => {
        toast.promise(stopNewsTicker(), {
            loading: "Stopping News Ticker...",
            success: () => {
                setIsTickerRunning(false);
                return "News Ticker Stopped";
            },
            error: "Failed to stop ticker",
        });
    };

    const updateSetting = (key: string, value: string) => {
        return updateNewsSetting(key, value);
    };

    // Handle adding a new source from dropdown
    const handleAddSource = (changes: any) => {
        const sourceKey = changes.value;
        if (!sourceKey || settings.newsSources.includes(sourceKey)) return;

        const newSources = [...settings.newsSources, sourceKey];
        const newSettings = { ...settings, newsSources: newSources };
        setSettings(newSettings);
        updateSetting("newsSources", sourcesToString(newSources));

        // Auto-restart if running
        if (isTickerRunning) {
            restartTicker(newSettings);
        }
    };

    // Handle removing a source
    const handleRemoveSource = (sourceKey: string) => {
        const newSources = settings.newsSources.filter((s) => s !== sourceKey);
        if (newSources.length === 0) {
            toast.error("Must have at least one news source");
            return;
        }
        const newSettings = { ...settings, newsSources: newSources };
        setSettings(newSettings);
        updateSetting("newsSources", sourcesToString(newSources));

        // Auto-restart if running
        if (isTickerRunning) {
            restartTicker(newSettings);
        }
    };

    // Handle custom URL change
    const handleSetCustomURL = () => {
        const url = customURLInput.trim();
        if (!url) {
            toast.error("Please enter a URL");
            return;
        }

        updateSetting("newsCustomURL", url);
        const newSettings = { ...settings, newsCustomURL: url };
        setSettings(newSettings);
        toast.success("Custom URL saved!");

        // If ticker is running with custom source, restart
        if (isTickerRunning && settings.newsSources.includes("custom")) {
            restartTicker(newSettings);
        }
    };

    // Handle speed change from dropdown
    const handleSpeedChange = (changes: any) => {
        const speed = parseInt(changes.value, 10);
        const newSettings = { ...settings, newsScrollSpeed: speed };
        setSettings(newSettings);
        updateSetting("newsScrollSpeed", speed.toString());
        // No restart needed - backend reads scroll speed dynamically from config
    };

    // Handle refresh change from dropdown
    const handleRefreshChange = (changes: any) => {
        const refresh = parseInt(changes.value, 10);
        const newSettings = { ...settings, newsTickerRefresh: refresh };
        setSettings(newSettings);
        updateSetting("newsTickerRefresh", refresh.toString());

        // Auto-restart if running
        if (isTickerRunning) {
            restartTicker(newSettings);
        }
    };

    // Handle color change from dropdown
    const handleColorChange = (changes: any) => {
        const color = changes.value;
        const newSettings = { ...settings, newsTickerColor: color };
        setSettings(newSettings);
        updateSetting("newsTickerColor", color);

        // Auto-restart if running
        if (isTickerRunning) {
            restartTicker(newSettings);
        }
    };

    // Handle font change from dropdown
    const handleFontChange = (changes: any) => {
        const font = changes.value;
        const newSettings = { ...settings, newsTickerFont: font };
        setSettings(newSettings);
        updateSetting("newsTickerFont", font);

        // Auto-restart if running
        if (isTickerRunning) {
            restartTicker(newSettings);
        }
    };

    const availableOptions = getAvailableSourceOptions(settings.newsSources);

    return (
        <div className="widget-section">
            <h3 className="section-title">
                <RssFeed style={{ marginRight: 8, verticalAlign: "middle" }} />
                News Ticker
            </h3>

            {/* Selected Sources as Chips */}
            <div className="setting-row">
                <label className="setting-label">Selected Sources</label>
                <div className="chip-container">
                    {settings.newsSources.map((sourceKey) => (
                        <div key={sourceKey} className="source-chip">
                            <span>{getSourceName(sourceKey)}</span>
                            <button
                                className="chip-remove"
                                onClick={() => handleRemoveSource(sourceKey)}
                                title="Remove source"
                            >
                                <Close style={{ fontSize: 14 }} />
                            </button>
                        </div>
                    ))}
                </div>
            </div>

            {/* Add Source Dropdown */}
            {availableOptions.length > 0 && (
                <div className="setting-row">
                    <label className="setting-label">Add News Source</label>
                    <Select
                        key={`add-source-${settingsLoaded}-${settings.newsSources.length}`}
                        selectedValue=""
                        name="addSource"
                        options={[{ text: "+ Select a source to add...", value: "" }, ...availableOptions]}
                        caretIcon={<CaretIcon />}
                        modalCloseButton={<ModalCloseButton />}
                        onChange={handleAddSource}
                    />
                </div>
            )}

            {/* Custom URL Input (only show when custom is selected) */}
            {settings.newsSources.includes("custom") && (
                <div className="input-row">
                    <input
                        type="text"
                        value={customURLInput}
                        onChange={(e) => setCustomURLInput(e.target.value)}
                        onKeyPress={(e) => {
                            if (e.key === "Enter") {
                                handleSetCustomURL();
                            }
                        }}
                        placeholder="Enter RSS feed URL"
                        className="symbol-input"
                    />
                    <Button
                        variant="contained"
                        onClick={handleSetCustomURL}
                        className="add-button"
                    >
                        SET URL
                    </Button>
                </div>
            )}

            {/* Settings Row */}
            <div className="settings-grid">
                {/* Refresh Interval */}
                <div className="setting-item">
                    <label className="setting-label">Refresh Interval</label>
                    <Select
                        key={`news-refresh-${settingsLoaded}-${settings.newsTickerRefresh}`}
                        selectedValue={settings.newsTickerRefresh.toString()}
                        name="refreshInterval"
                        options={refreshPresets}
                        caretIcon={<CaretIcon />}
                        modalCloseButton={<ModalCloseButton />}
                        onChange={handleRefreshChange}
                    />
                </div>

                {/* Text Color */}
                <div className="setting-item">
                    <label className="setting-label">Text Color</label>
                    <Select
                        key={`news-color-${settingsLoaded}-${settings.newsTickerColor}`}
                        selectedValue={settings.newsTickerColor}
                        name="textColor"
                        options={colorPresets.map((c) => ({
                            value: c.value,
                            text: c.name,
                            markup: (
                                <div style={{ display: "flex", alignItems: "center" }}>
                                    <div
                                        style={{
                                            width: 16,
                                            height: 16,
                                            backgroundColor: `#${c.value}`,
                                            marginRight: 8,
                                            borderRadius: 2,
                                            border: c.value === "FFFFFF" ? "1px solid #666" : "none",
                                        }}
                                    />
                                    {c.name}
                                </div>
                            ),
                        }))}
                        caretIcon={<CaretIcon />}
                        modalCloseButton={<ModalCloseButton />}
                        onChange={handleColorChange}
                    />
                </div>

                {/* Scrolling Speed */}
                <div className="setting-item">
                    <label className="setting-label">Scrolling Speed</label>
                    <Select
                        key={`news-speed-${settingsLoaded}-${settings.newsScrollSpeed}`}
                        selectedValue={settings.newsScrollSpeed.toString()}
                        name="scrollSpeed"
                        options={speedPresets}
                        caretIcon={<CaretIcon />}
                        modalCloseButton={<ModalCloseButton />}
                        onChange={handleSpeedChange}
                    />
                </div>

                {/* Font */}
                <div className="setting-item">
                    <label className="setting-label">Font</label>
                    <Select
                        key={`news-font-${settingsLoaded}-${settings.newsTickerFont}`}
                        selectedValue={settings.newsTickerFont}
                        name="tickerFont"
                        options={getFontOptions()}
                        caretIcon={<CaretIcon />}
                        modalCloseButton={<ModalCloseButton />}
                        onChange={handleFontChange}
                    />
                </div>
            </div>

            {/* Start Button */}
            <Button
                variant="contained"
                onClick={isTickerRunning ? handleStopTicker : handleStartTicker}
                disabled={isLoading || (settings.newsSources.includes("custom") && !customURLInput.trim()) || settings.newsSources.length === 0}
                className={`start-button ${isTickerRunning ? "stop" : ""}`}
            >
                {isLoading ? (
                    <CircularProgress size={24} style={{ color: "#fff", marginRight: 8 }} />
                ) : isTickerRunning ? (
                    <Stop />
                ) : (
                    <PlayArrow />
                )}
                {isLoading ? "PLEASE WAIT..." : isTickerRunning ? "STOP NEWS TICKER" : "START NEWS TICKER"}
            </Button>
        </div>
    );
};

export default NewsWidget;
