import { Button } from "@material-ui/core";
import React, { useEffect, useState, ReactElement } from "react";
import toast from "react-hot-toast";
import { checkForUpdates } from "../../services";
import { updateFirmware } from "../../services/settings";
import "./styles.css";

const UpdatePage = (): ReactElement => {
  const [updateInfo, setUpdateInfo] = useState<any>(null);
  const [checkingUpdate, setCheckingUpdate] = useState(true);
  const [checkFailed, setCheckFailed] = useState(false);
  const [isUpdating, setIsUpdating] = useState(false);

  const handleCheckForUpdates = async () => {
    setCheckingUpdate(true);
    setCheckFailed(false);
    try {
      const info = await checkForUpdates();
      setUpdateInfo(info);
    } catch (error) {
      console.error(error);
      setCheckFailed(true);
    } finally {
      setCheckingUpdate(false);
    }
  };

  useEffect(() => {
    handleCheckForUpdates();
  }, []);

  const updateSystem = (beta: boolean) => {
    setIsUpdating(true);
    toast.promise(
      updateFirmware(beta),
      {
        loading: beta ? "Pixelcade LCD Beta Update Started" : "Pixelcade LCD Update Started",
        success: () => {
          return `Please wait until you see the Update Complete message on your Pixelcade before doing anything else.`;
        },
        error: "Failed to start update",
      },
      { duration: 10000 }
    );
  };

  // Check if beta is available and different from stable
  const betaAvailable = updateInfo?.betaFirmwareVersion &&
    updateInfo.betaFirmwareVersion !== updateInfo.latestFirmwareVersion;

  return (
    <div className="fade-in update-page">
      <div className="update-container">
        <h1 className="update-title">System Updates</h1>

        {checkingUpdate ? (
          <div className="update-status checking">
            <div className="spinner"></div>
            <p>Checking for updates...</p>
          </div>
        ) : checkFailed ? (
          // USB-only connectivity - can't check for updates
          <div className="update-status usb-only">
            <div className="usb-icon">USB</div>
            <h2>USB Connection Only</h2>
            <p>WiFi connectivity is required for updates.</p>
            <p className="usb-instructions">
              Since you are connected via USB only, you can still update by following these instructions:
            </p>
            <a
              href="https://pixelcade.org/lcdgen3/"
              target="_blank"
              rel="noopener noreferrer"
              className="usb-link"
            >
              pixelcade.org/lcdgen3
            </a>
          </div>
        ) : updateInfo ? (
          <>
            {updateInfo.updateAvailable ? (
              <div className="update-status available">
                <div className="update-badge">
                  <span className="badge-icon">!</span>
                </div>
                <h2>Updates Available</h2>

                <div className="update-details">
                  {updateInfo.firmwareUpdateAvailable && (
                    <div className="update-item">
                      <span className="update-label">Firmware</span>
                      <span className="update-versions">
                        <span className="version-current">{updateInfo.currentFirmwareVersion}</span>
                        <span className="version-arrow"></span>
                        <span className="version-new">{updateInfo.latestFirmwareVersion}</span>
                      </span>
                    </div>
                  )}
                  {updateInfo.artworkUpdateAvailable && (
                    <div className="update-item">
                      <span className="update-label">Artwork</span>
                      <span className="update-versions">
                        <span className="version-current">v{updateInfo.currentArtworkVersion}</span>
                        <span className="version-arrow"></span>
                        <span className="version-new">v{updateInfo.latestArtworkVersion}</span>
                      </span>
                    </div>
                  )}
                </div>

                {(updateInfo.releaseNotes || updateInfo.releaseDate) && (
                  <div className="release-notes">
                    <h3>What's New</h3>
                    {updateInfo.releaseDate && (
                      <p className="release-date">Release Date: {updateInfo.releaseDate}</p>
                    )}
                    {updateInfo.releaseNotes && (
                      <p className="release-notes-text">{updateInfo.releaseNotes}</p>
                    )}
                  </div>
                )}

                <div className="update-actions">
                  <Button
                    variant="contained"
                    className="install-button"
                    onClick={() => updateSystem(false)}
                    disabled={isUpdating}
                    style={{ backgroundColor: "#4caf50", color: "#fff" }}
                  >
                    {isUpdating ? "Update Started..." : "Install Update"}
                  </Button>
                </div>

                <p className="update-warning">
                  Updates can take up to 15 minutes or more depending on your Internet connection.
                  Please wait until you see the Update Complete message on your Pixelcade.
                </p>
              </div>
            ) : (
              <div className="update-status up-to-date">
                <div className="checkmark"></div>
                <h2>You're Up to Date!</h2>
                <p>Your Pixelcade is running the latest stable version.</p>
              </div>
            )}

            {/* Beta section - only show if beta is different from stable */}
            {betaAvailable && (
              <div className="beta-section">
                <h3>Beta Version Available</h3>
                <div className="beta-version-info">
                  <span className="beta-label">Beta Firmware:</span>
                  <span className="beta-version">{updateInfo.betaFirmwareVersion}</span>
                </div>
                <p className="beta-description">
                  Get access to pre-release versions with the latest features and improvements.
                  Beta versions may contain bugs.
                </p>
                <Button
                  variant="contained"
                  className="beta-button"
                  onClick={() => updateSystem(true)}
                  disabled={isUpdating}
                >
                  {isUpdating ? "Update Started..." : "Install Beta"}
                </Button>
              </div>
            )}

            <div className="current-versions">
              <h3>Current Versions</h3>
              <div className="version-info">
                <span className="version-label">Firmware:</span>
                <span className="version-value">{updateInfo.currentFirmwareVersion}</span>
              </div>
              <div className="version-info">
                <span className="version-label">Artwork:</span>
                <span className="version-value">v{updateInfo.currentArtworkVersion}</span>
              </div>
            </div>
          </>
        ) : (
          <div className="update-status error">
            <p>Unable to check for updates. Please check your network connection.</p>
          </div>
        )}

        <div className="update-actions" style={{ marginTop: "2rem" }}>
          <Button
            variant="contained"
            color="primary"
            onClick={handleCheckForUpdates}
            disabled={checkingUpdate}
          >
            {checkingUpdate ? "Checking..." : "Check Again"}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default UpdatePage;
