import React, { useState } from "react";
import { Dialog, DialogTitle, DialogContent, DialogActions, Button, TextField } from "@material-ui/core";
import { MarqueeInfo } from "../../types/DeviceInfo.type";
import { getDeviceUrl, updateMarquee } from "../../services";
import toast from "react-hot-toast";
import { useAppSettings } from "../../App";

type MarqueeDetailsModalProps = {
    open: boolean;
    onClose: () => void;
    marquee: MarqueeInfo;
    system: string;
    onDelete?: () => void;
};

const MarqueeDetailsModal: React.FC<MarqueeDetailsModalProps> = ({ open, onClose, marquee, system, onDelete }) => {
    const [symlinkName, setSymlinkName] = useState<string>("");
    const [isCreatingSymlink, setIsCreatingSymlink] = useState<boolean>(false);
    const [isDeleting, setIsDeleting] = useState<boolean>(false);
    const [confirmingDelete, setConfirmingDelete] = useState<boolean>(false);
    const { streamImages } = useAppSettings();
    const hostname = window.location.hostname;
    const isUserUploaded = marquee.storageType === "usb" || marquee.storageType === "internal";

    // Construct the full filename: system-gamename.jpg
    const sourceFilename = `${system}-${marquee.filename}.jpg`;
    const fullImageUrl = `${getDeviceUrl("")}/lcdmarquees/${sourceFilename}`;

    // Reset confirmation state when modal closes
    const handleClose = () => {
        setConfirmingDelete(false);
        onClose();
    };

    const handleDownload = async () => {
        try {
            toast.loading(`Downloading ${sourceFilename}...`);
            const response = await fetch(fullImageUrl);

            if (!response.ok) {
                throw new Error(`Failed to fetch image: ${response.status}`);
            }

            const blob = await response.blob();
            const url = window.URL.createObjectURL(blob);
            const link = document.createElement("a");
            link.href = url;
            link.download = sourceFilename;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            window.URL.revokeObjectURL(url);

            toast.dismiss();
            toast.success(`Downloaded ${sourceFilename}`);
        } catch (error) {
            toast.dismiss();
            toast.error("Failed to download image");
            console.error(error);
        }
    };

    const handleCreateSymlink = async () => {
        if (!symlinkName.trim()) {
            toast.error("Please enter an alias name");
            return;
        }

        const newFilename = symlinkName.trim().endsWith('.jpg')
            ? symlinkName.trim()
            : `${symlinkName.trim()}.jpg`;

        setIsCreatingSymlink(true);

        try {
            const response = await fetch(
                `${getDeviceUrl("")}/symlink?original=${encodeURIComponent(sourceFilename)}&new=${encodeURIComponent(newFilename)}`
            );

            const data = await response.json();

            if (response.ok && data.success) {
                toast.success(`Alias created: ${data.symlink}`);
                setSymlinkName("");
            } else {
                toast.error(data.error || "Failed to create alias");
            }
        } catch (error) {
            toast.error("Error creating alias");
            console.error(error);
        } finally {
            setIsCreatingSymlink(false);
        }
    };

    const handleSetMarquee = () => {
        // Use the /arcade/stream endpoint with system and filename
        toast.promise(updateMarquee(system, marquee.filename, streamImages), {
            loading: "Updating Marquee",
            success: "Successfully Updated Marquee",
            error: "Couldn't set the marquee",
        });
        handleClose();
    };

    const handleDeleteClick = () => {
        if (!isUserUploaded) {
            toast.error("Can only delete user-uploaded marquees");
            return;
        }
        setConfirmingDelete(true);
    };

    const handleDeleteConfirm = async () => {
        setIsDeleting(true);

        try {
            const response = await fetch(
                `${getDeviceUrl("")}/v2/artwork/delete?filename=${encodeURIComponent(sourceFilename)}&storage=${encodeURIComponent(marquee.storageType || "")}`
            );

            const data = await response.json();

            if (response.ok && data.status === "ok") {
                toast.success(`Deleted ${marquee.name}`);
                // Call onDelete first to update the list, then close the modal
                if (onDelete) {
                    onDelete();
                }
                handleClose();
            } else {
                toast.error(data.error || "Failed to delete marquee");
            }
        } catch (error) {
            toast.error("Error deleting marquee");
            console.error(error);
        } finally {
            setIsDeleting(false);
            setConfirmingDelete(false);
        }
    };

    return (
        <Dialog open={open} onClose={handleClose} maxWidth="sm" fullWidth>
            <DialogTitle style={{ backgroundColor: "#6b0078", color: "#fff", position: "relative", paddingRight: "48px" }}>
                Marquee Details
                <div
                    onClick={handleClose}
                    style={{
                        position: "absolute",
                        top: "8px",
                        right: "8px",
                        width: "32px",
                        height: "32px",
                        borderRadius: "50%",
                        backgroundColor: "rgba(0,0,0,.3)",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        cursor: "pointer",
                        transition: "all 0.2s ease",
                        border: "1px solid rgba(255,255,255,.3)",
                    }}
                    onMouseEnter={(e) => {
                        e.currentTarget.style.backgroundColor = "rgba(0,0,0,.6)";
                        e.currentTarget.style.transform = "scale(1.1)";
                    }}
                    onMouseLeave={(e) => {
                        e.currentTarget.style.backgroundColor = "rgba(0,0,0,.3)";
                        e.currentTarget.style.transform = "scale(1)";
                    }}
                >
                    <span style={{
                        color: "#fff",
                        fontSize: "20px",
                        fontWeight: "bold",
                        lineHeight: "1",
                    }}>
                        
                    </span>
                </div>
            </DialogTitle>
            <DialogContent style={{ backgroundColor: "#2a2a2a", color: "#fff", paddingTop: "20px" }}>
                <div style={{ marginBottom: "20px", textAlign: "center" }}>
                    <img
                        src={fullImageUrl}
                        alt={marquee.name}
                        style={{
                            maxWidth: "100%",
                            maxHeight: "300px",
                            objectFit: "contain",
                            borderRadius: "4px",
                        }}
                        onError={(e) => {
                            (e.target as HTMLImageElement).src = `${getDeviceUrl()}/thumbnails/${marquee.apiSystem}/${marquee.filename}`;
                        }}
                    />
                </div>

                <div style={{ marginBottom: "20px" }}>
                    <p style={{ margin: "8px 0" }}>
                        <strong>Name:</strong> {marquee.name}
                    </p>
                    <p style={{ margin: "8px 0" }}>
                        <strong>Source File:</strong> {sourceFilename}
                    </p>
                    <p style={{ margin: "8px 0" }}>
                        <strong>System:</strong> {marquee.systemName}
                    </p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                    <Button
                        variant="contained"
                        onClick={handleDownload}
                        fullWidth
                        style={{
                            backgroundColor: "#4c3ba2",
                            color: "#fff",
                            marginBottom: "10px",
                        }}
                    >
                        Download Full Resolution Image
                    </Button>
                </div>

                <div style={{ borderTop: "1px solid #444", paddingTop: "20px" }}>
                    <p style={{ marginBottom: "10px", fontWeight: 600 }}>Create Alias:</p>
                    <TextField
                        fullWidth
                        variant="outlined"
                        placeholder="Enter alternate filename (e.g., mame-pacman2.jpg)"
                        value={symlinkName}
                        onChange={(e) => setSymlinkName(e.target.value)}
                        onKeyPress={(e) => {
                            if (e.key === 'Enter') {
                                handleCreateSymlink();
                            }
                        }}
                        disabled={isCreatingSymlink}
                        style={{ marginBottom: "10px" }}
                        InputProps={{
                            style: { backgroundColor: "#fff" }
                        }}
                    />
                    <Button
                        variant="contained"
                        onClick={handleCreateSymlink}
                        fullWidth
                        disabled={isCreatingSymlink}
                        style={{
                            backgroundColor: "#6b0078",
                            color: "#fff",
                        }}
                    >
                        {isCreatingSymlink ? "Creating..." : "Create Alias"}
                    </Button>
                </div>

                {/* Delete section - only for user uploads */}
                {isUserUploaded && (
                    <div style={{ borderTop: "1px solid #444", paddingTop: "20px", marginTop: "20px" }}>
                        <p style={{ marginBottom: "10px", fontWeight: 600, color: "#ef4444" }}>Danger Zone:</p>
                        {!confirmingDelete ? (
                            <Button
                                variant="contained"
                                onClick={handleDeleteClick}
                                fullWidth
                                disabled={isDeleting}
                                style={{
                                    backgroundColor: "#dc2626",
                                    color: "#fff",
                                }}
                            >
                                Delete Marquee
                            </Button>
                        ) : (
                            <div>
                                <p style={{ marginBottom: "10px", color: "#fff" }}>
                                    Are you sure you want to delete "{marquee.name}"?
                                </p>
                                <div style={{ display: "flex", gap: "10px" }}>
                                    <Button
                                        variant="contained"
                                        onClick={handleDeleteConfirm}
                                        disabled={isDeleting}
                                        style={{
                                            backgroundColor: "#dc2626",
                                            color: "#fff",
                                            flex: 1,
                                        }}
                                    >
                                        {isDeleting ? "Deleting..." : "Yes, Delete"}
                                    </Button>
                                    <Button
                                        variant="outlined"
                                        onClick={() => setConfirmingDelete(false)}
                                        disabled={isDeleting}
                                        style={{
                                            borderColor: "#666",
                                            color: "#fff",
                                            flex: 1,
                                        }}
                                    >
                                        Cancel
                                    </Button>
                                </div>
                            </div>
                        )}
                        <p style={{ marginTop: "8px", fontSize: "12px", color: "#888" }}>
                            This will permanently delete this user-uploaded marquee.
                        </p>
                    </div>
                )}
            </DialogContent>
            <DialogActions style={{ backgroundColor: "#2a2a2a", padding: "16px" }}>
                <Button onClick={handleClose} style={{ color: "#fff" }}>
                    Close
                </Button>
                <Button
                    onClick={handleSetMarquee}
                    variant="contained"
                    style={{ backgroundColor: "#6b0078", color: "#fff" }}
                >
                    SET AS CURRENT MARQUEE
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default MarqueeDetailsModal;
