// Favorite team type
type FavoriteTeam = {
    league: string;
    team: string;
};

interface SportsTickerWidgetProps {
    onTickerStart?: () => void;
}

const SportsTickerWidget: React.FC<SportsTickerWidgetProps> = ({ onTickerStart }) => {
    const [settings, setSettings] = useState<SportsSettings>({
        sportLeagues: "",
        sportTeamCodes: "",
        sportTickerRefresh: 60, // Default 1 minute (stored in seconds)
        sportScrollSpeed: 5,    // Default "Normal" speed
        sportFavoriteTeams: "",
        sportTickerMode: "league",
        sportDarkMode: false,
    });
    const [isTickerRunning, setIsTickerRunning] = useState(false);
    const [selectedLeague, setSelectedLeague] = useState<string>("");
    const [selectedTeams, setSelectedTeams] = useState<string[]>([]);
    const [scrollSpeed, setScrollSpeed] = useState<number>(5); // Default "Normal" speed
    const [favoriteTeams, setFavoriteTeams] = useState<FavoriteTeam[]>([]);
    const [mode, setMode] = useState<"league" | "favorites">("league");
    const [favLeagueSelect, setFavLeagueSelect] = useState<string>("");
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [settingsLoaded, setSettingsLoaded] = useState<boolean>(false);

    // Parse favorite teams string into array
    const parseFavoriteTeams = (str: string): FavoriteTeam[] => {
        if (!str) return [];
        return str.split(",").map(part => {
            const [league, team] = part.split(":");
            return { league: league?.toLowerCase() || "", team: team?.toUpperCase() || "" };
        }).filter(f => f.league && f.team);
    };

    // Convert favorite teams array to string
    const favoritesToString = (favs: FavoriteTeam[]): string => {
        return favs.map(f => `${f.league}:${f.team}`).join(",");
    };

    // Convert seconds to dropdown value string - presets are now in seconds
    const secondsToValueString = (seconds: number): string => {
        // Match to closest preset (all in seconds now)
        const presets = [15, 30, 60, 120, 180, 240, 300, 900, 1800, 3600, 7200];
        const closest = presets.reduce((prev, curr) =>
            Math.abs(curr - seconds) < Math.abs(prev - seconds) ? curr : prev
        );
        return closest.toString();
    };

    // Load settings on mount
    useEffect(() => {
        // First load saved settings, then check ticker status
        readSettings()
            .then((data: any) => {
                if (data) {
                    const sportLeagues = data.sportLeagues || "";
                    const sportTeamCodes = data.sportTeamCodes || "";
                    const sportFavoriteTeams = data.sportFavoriteTeams || "";
                    const sportScrollSpeed = data.sportScrollSpeed || 5;  // Default "Normal" speed
                    const sportTickerRefresh = data.sportTickerRefresh || 60;  // Default 1 minute (seconds)
                    const sportTickerMode = (data.sportTickerMode === "favorites" ? "favorites" : "league") as "league" | "favorites";
                    const sportDarkMode = data.sportDarkMode === true;

                    console.log("[SportsTickerWidget] Loaded settings:", {
                        sportLeagues,
                        sportTeamCodes,
                        sportScrollSpeed,
                        sportTickerRefresh,
                        sportTickerMode,
                        sportFavoriteTeams,
                        sportDarkMode
                    });

                    setSettings({
                        sportLeagues,
                        sportTeamCodes,
                        sportTickerRefresh,
                        sportScrollSpeed,
                        sportFavoriteTeams,
                        sportTickerMode,
                        sportDarkMode,
                    });
                    setSelectedLeague(sportLeagues);
                    setScrollSpeed(sportScrollSpeed);
                    setMode(sportTickerMode);
                    if (sportTeamCodes) {
                        setSelectedTeams(sportTeamCodes.split(",").map((s: string) => s.trim()).filter((s: string) => s));
                    }
                    if (sportFavoriteTeams) {
                        setFavoriteTeams(parseFavoriteTeams(sportFavoriteTeams));
                    }
                    setSettingsLoaded(true);
                }

                // After loading settings, check ticker running status (don't override settings)
                return getSportsTickerStatus();
            })
            .then((statusData: any) => {
                if (statusData) {
                    setIsTickerRunning(statusData.running || false);
                }
            })
            .catch((err) => {
                console.error("[SportsTickerWidget] Error loading settings:", err);
                setIsTickerRunning(false);
            });

        // Listen for other tickers starting - reset our button state
        const unsubscribe = onTickerStarted((tickerType) => {
            if (tickerType !== 'sports') {
                setIsTickerRunning(false);
            }
        });

        return () => unsubscribe();
    }, []);

    // Get teams for selected league
    const getTeamOptions = () => {
        if (!selectedLeague) return [];
        const teams = teamsByLeague[selectedLeague];
        if (!teams) return [];
        return [{ text: "All Teams (Show All Games)", value: "" }, ...teams];
    };

    // Get teams for favorite league selection
    const getFavTeamOptions = () => {
        if (!favLeagueSelect) return [];
        const teams = teamsByLeague[favLeagueSelect];
        if (!teams) return [];
        return [{ text: "Select a Team...", value: "" }, ...teams];
    };

    // Restart ticker with current settings
    // Note: refresh is in seconds, but API expects minutes
    const restartTicker = async (league: string, teams: string[], refreshSeconds: number, speed?: number) => {
        if (!league) return;

        try {
            const refreshMinutes = refreshSeconds / 60;
            await startSportsTicker(league, teams, refreshMinutes, speed ?? scrollSpeed);
            setIsTickerRunning(true);
            dispatchTickerStarted('sports');
        } catch (error) {
            console.error("Failed to restart sports ticker", error);
        }
    };

    // Poll for ready status - returns when ticker is ready or times out
    const waitForReady = async (maxWaitMs: number = 30000): Promise<boolean> => {
        const pollInterval = 500; // Poll every 500ms
        const maxAttempts = Math.floor(maxWaitMs / pollInterval);

        for (let i = 0; i < maxAttempts; i++) {
            try {
                const status = await getSportsTickerStatus();
                if (status.ready) {
                    return true;
                }
            } catch (err) {
                // Ignore errors during polling
            }
            await new Promise(resolve => setTimeout(resolve, pollInterval));
        }
        return false; // Timed out
    };

    const handleStartTicker = async () => {
        // Convert settings.sportTickerRefresh (seconds) to minutes for API
        const refreshMinutes = settings.sportTickerRefresh / 60;

        if (mode === "favorites") {
            if (favoriteTeams.length === 0) {
                toast.error("Please add at least one favorite team");
                return;
            }
            const favsStr = favoritesToString(favoriteTeams);
            setIsLoading(true);
            try {
                await startFavoritesTicker(favsStr, refreshMinutes, scrollSpeed);
                setIsTickerRunning(true);
                dispatchTickerStarted('sports');
                onTickerStart?.();
                // Wait for pre-render to complete
                await waitForReady();
                toast.success("Sports Ticker Started!");
            } catch (error) {
                toast.error("Failed to start ticker");
            } finally {
                setIsLoading(false);
            }
        } else {
            if (!selectedLeague) {
                toast.error("Please select a league");
                return;
            }

            setIsLoading(true);
            try {
                await startSportsTicker(selectedLeague, selectedTeams, refreshMinutes, scrollSpeed);
                setIsTickerRunning(true);
                dispatchTickerStarted('sports');
                onTickerStart?.();
                // Wait for pre-render to complete
                await waitForReady();
                toast.success("Sports Ticker Started!");
            } catch (error) {
                toast.error("Failed to start ticker");
            } finally {
                setIsLoading(false);
            }
        }
    };

    const handleStopTicker = () => {
        toast.promise(stopSportsTicker(), {
            loading: "Stopping Sports Ticker...",
            success: () => {
                setIsTickerRunning(false);
                return "Sports Ticker Stopped";
            },
            error: "Failed to stop ticker",
        });
    };

    const updateSetting = (key: string, value: string) => {
        return updateSportsSetting(key, value);
    };

    // Handle league change
    const handleLeagueChange = (changes: any) => {
        const league = changes.value;
        setSelectedLeague(league);
        setSelectedTeams([]); // Clear teams when league changes

        const newSettings = { ...settings, sportLeagues: league };
        setSettings(newSettings);
        updateSetting("sportLeagues", league);
        updateSetting("sportTeamCodes", "");

        // Auto-restart if running
        if (isTickerRunning && mode === "league") {
            restartTicker(league, [], settings.sportTickerRefresh);
        }
    };

    // Handle team selection
    const handleTeamChange = (changes: any) => {
        const team = changes.value;
        if (!team) {
            // "All Teams" selected
            setSelectedTeams([]);
            const newSettings = { ...settings, sportTeamCodes: "" };
            setSettings(newSettings);
            updateSetting("sportTeamCodes", "");

            if (isTickerRunning && mode === "league") {
                restartTicker(selectedLeague, [], settings.sportTickerRefresh);
            }
            return;
        }

        // Add team if not already in list
        if (selectedTeams.includes(team)) {
            toast.error("Team already selected");
            return;
        }

        const newTeams = [...selectedTeams, team];
        setSelectedTeams(newTeams);

        const teamsStr = newTeams.join(",");
        const newSettings = { ...settings, sportTeamCodes: teamsStr };
        setSettings(newSettings);
        updateSetting("sportTeamCodes", teamsStr);

        // Auto-restart if running
        if (isTickerRunning && mode === "league") {
            restartTicker(selectedLeague, newTeams, settings.sportTickerRefresh);
        }
    };

    // Remove a team
    const handleRemoveTeam = (team: string) => {
        const newTeams = selectedTeams.filter((t) => t !== team);
        setSelectedTeams(newTeams);

        const teamsStr = newTeams.join(",");
        const newSettings = { ...settings, sportTeamCodes: teamsStr };
        setSettings(newSettings);
        updateSetting("sportTeamCodes", teamsStr);

        // Auto-restart if running
        if (isTickerRunning && selectedLeague && mode === "league") {
            restartTicker(selectedLeague, newTeams, settings.sportTickerRefresh);
        }
    };

    // Handle refresh change - values are already in seconds from preset
    const handleRefreshChange = (changes: any) => {
        const refreshSeconds = parseInt(changes.value, 10);
        const newSettings = { ...settings, sportTickerRefresh: refreshSeconds };
        setSettings(newSettings);
        // Save as seconds to backend
        updateSetting("sportTickerRefresh", refreshSeconds.toString());

        // Auto-restart if running - convert seconds to minutes for API
        if (isTickerRunning) {
            const refreshMinutes = refreshSeconds / 60;
            if (mode === "favorites") {
                const favsStr = favoritesToString(favoriteTeams);
                startFavoritesTicker(favsStr, refreshMinutes, scrollSpeed);
            } else if (selectedLeague) {
                restartTicker(selectedLeague, selectedTeams, refreshMinutes);
            }
        }
    };

    // Handle scroll speed change (from dropdown)
    // Note: Speed change is applied in real-time by the backend without restart
    const handleScrollSpeedChange = (changes: any) => {
        const speed = parseInt(changes.value, 10);
        setScrollSpeed(speed);
        const newSettings = { ...settings, sportScrollSpeed: speed };
        setSettings(newSettings);
        updateSetting("sportScrollSpeed", changes.value);
        // No restart needed - backend reads scroll speed dynamically from config
    };

    // Get scroll speed display value for dropdown
    const getScrollSpeedValue = (speed: number): string => {
        // Find closest preset
        const presets = [2, 5, 10, 15];
        const closest = presets.reduce((prev, curr) =>
            Math.abs(curr - speed) < Math.abs(prev - speed) ? curr : prev
        );
        return closest.toString();
    };

    // Add favorite team
    const handleAddFavorite = (changes: any) => {
        const team = changes.value;
        if (!team || !favLeagueSelect) return;

        // Check if already exists
        const exists = favoriteTeams.some(f => f.league === favLeagueSelect && f.team === team);
        if (exists) {
            toast.error("Team already in favorites");
            return;
        }

        const newFavorites = [...favoriteTeams, { league: favLeagueSelect, team }];
        setFavoriteTeams(newFavorites);

        const favsStr = favoritesToString(newFavorites);
        setSettings({ ...settings, sportFavoriteTeams: favsStr });
        updateSetting("sportFavoriteTeams", favsStr);
    };

    // Remove favorite team
    const handleRemoveFavorite = (fav: FavoriteTeam) => {
        const newFavorites = favoriteTeams.filter(f => !(f.league === fav.league && f.team === fav.team));
        setFavoriteTeams(newFavorites);

        const favsStr = favoritesToString(newFavorites);
        setSettings({ ...settings, sportFavoriteTeams: favsStr });
        updateSetting("sportFavoriteTeams", favsStr);
    };

    // Get team name for display
    const getTeamDisplayName = (teamCode: string, league?: string): string => {
        const leagueToUse = league || selectedLeague;
        if (!leagueToUse) return teamCode;
        const teams = teamsByLeague[leagueToUse];
        if (!teams) return teamCode;
        const team = teams.find((t) => t.value === teamCode);
        return team ? team.text : teamCode;
    };

    // Get league display name
    const getLeagueDisplayName = (leagueCode: string): string => {
        const league = leagueOptions.find(l => l.value === leagueCode);
        return league ? league.text : leagueCode.toUpperCase();
    };

    // Handle dark mode toggle
    const handleDarkModeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const enabled = event.target.checked;
        const newSettings = { ...settings, sportDarkMode: enabled };
        setSettings(newSettings);
        updateSetting("sportDarkMode", enabled ? "yes" : "no");
    };

    // Is single team mode (for scroll speed slider disable state)
    // Only true when EXACTLY 1 team is selected - multiple teams should use scrolling cards view
    const isSingleTeamMode = mode === "league" && selectedTeams.length === 1;

    return (
        <div className="widget-section">
            <h3 className="section-title">
                <SportsBasketball style={{ marginRight: 8, verticalAlign: "middle" }} />
                Live Sports Scores
            </h3>

            {/* Mode Toggle */}
            <div className="setting-row" style={{ marginBottom: "1rem" }}>
                <div style={{ display: "flex", gap: "1rem" }}>
                    <Button
                        variant={mode === "league" ? "contained" : "outlined"}
                        onClick={() => {
                            setMode("league");
                            setSettings({ ...settings, sportTickerMode: "league" });
                            updateSetting("sportTickerMode", "league");
                        }}
                        style={{
                            backgroundColor: mode === "league" ? "#4169E1" : "transparent",
                            color: "#fff",
                            borderColor: "#4169E1",
                        }}
                    >
                        Single League
                    </Button>
                    <Button
                        variant={mode === "favorites" ? "contained" : "outlined"}
                        onClick={() => {
                            setMode("favorites");
                            setSettings({ ...settings, sportTickerMode: "favorites" });
                            updateSetting("sportTickerMode", "favorites");
                        }}
                        style={{
                            backgroundColor: mode === "favorites" ? "#4169E1" : "transparent",
                            color: "#fff",
                            borderColor: "#4169E1",
                        }}
                    >
                        My Favorite Teams
                    </Button>
                </div>
            </div>

            {mode === "league" ? (
                <>
                    {/* League and Team Selection on same row */}
                    <div className="setting-row" style={{ display: "flex", gap: "1rem", flexWrap: "wrap" }}>
                        <div style={{ flex: 1, minWidth: "180px" }}>
                            <label className="setting-label">Select League</label>
                            <Select
                                key={`league-select-${settingsLoaded}-${selectedLeague}`}
                                name="league"
                                options={[
                                    { text: "Select a League...", value: "" },
                                    ...leagueOptions.map((l) => ({ text: l.text, value: l.value })),
                                ]}
                                caretIcon={<CaretIcon />}
                                modalCloseButton={<ModalCloseButton />}
                                onChange={handleLeagueChange}
                                selectedValue={selectedLeague}
                            />
                        </div>

                        {/* Team Selection (only show if league is selected) */}
                        {selectedLeague && teamsByLeague[selectedLeague] && (
                            <div style={{ flex: 1, minWidth: "180px" }}>
                                <label className="setting-label">Filter by Team (optional)</label>
                                <Select
                                    key={`team-select-${selectedLeague}`}
                                    name="team"
                                    options={getTeamOptions()}
                                    caretIcon={<CaretIcon />}
                                    modalCloseButton={<ModalCloseButton />}
                                    onChange={handleTeamChange}
                                    selectedValue=""
                                />
                            </div>
                        )}
                    </div>

                    {/* Selected Teams Chips */}
                    {selectedTeams.length > 0 && (
                        <div className="symbol-chips">
                            {selectedTeams.map((team) => (
                                <div key={team} className="symbol-chip stock">
                                    {getTeamDisplayName(team)}
                                    <span
                                        onClick={() => handleRemoveTeam(team)}
                                        className="chip-close"
                                        style={{ marginLeft: 8, cursor: "pointer" }}
                                    >
                                        &times;
                                    </span>
                                </div>
                            ))}
                        </div>
                    )}
                </>
            ) : (
                <>
                    {/* Favorites Mode */}
                    <div className="setting-row">
                        <label className="setting-label">Add Favorite Teams (across any league)</label>
                        <div style={{ display: "flex", gap: "0.5rem", flexWrap: "wrap" }}>
                            <div style={{ flex: 1, minWidth: "150px" }}>
                                <Select
                                    name="favLeague"
                                    options={[
                                        { text: "Select League...", value: "" },
                                        ...leagueOptions.filter(l => teamsByLeague[l.value]).map((l) => ({ text: l.text, value: l.value })),
                                    ]}
                                    caretIcon={<CaretIcon />}
                                    modalCloseButton={<ModalCloseButton />}
                                    onChange={(changes: any) => setFavLeagueSelect(changes.value)}
                                    selectedValue={favLeagueSelect}
                                />
                            </div>
                            {favLeagueSelect && (
                                <div style={{ flex: 1, minWidth: "150px" }}>
                                    <Select
                                        key={`fav-team-select-${favLeagueSelect}`}
                                        name="favTeam"
                                        options={getFavTeamOptions()}
                                        caretIcon={<CaretIcon />}
                                        modalCloseButton={<ModalCloseButton />}
                                        onChange={handleAddFavorite}
                                        selectedValue=""
                                    />
                                </div>
                            )}
                        </div>
                    </div>

                    {/* Favorite Teams Chips */}
                    {favoriteTeams.length > 0 && (
                        <div className="symbol-chips">
                            {favoriteTeams.map((fav, idx) => (
                                <div key={`${fav.league}-${fav.team}-${idx}`} className="symbol-chip crypto">
                                    <span style={{ fontSize: "0.7rem", opacity: 0.7 }}>
                                        {getLeagueDisplayName(fav.league)}:
                                    </span>{" "}
                                    {getTeamDisplayName(fav.team, fav.league)}
                                    <span
                                        onClick={() => handleRemoveFavorite(fav)}
                                        className="chip-close"
                                        style={{ marginLeft: 8, cursor: "pointer" }}
                                    >
                                        &times;
                                    </span>
                                </div>
                            ))}
                        </div>
                    )}

                    {/* Note about game availability */}
                    <p style={{ fontSize: "0.8rem", color: "#aaa", marginTop: "0.5rem", fontStyle: "italic" }}>
                        Note: Teams will only appear if they have a game today or an upcoming game
                    </p>
                </>
            )}

            {/* Settings Row - Refresh Interval, Scroll Speed, and Dark Mode */}
            <div className="setting-row" style={{ display: "flex", gap: "1rem", flexWrap: "wrap", alignItems: "flex-end" }}>
                {/* Refresh Interval */}
                <div style={{ flex: 1, minWidth: "150px" }}>
                    <label className="setting-label">Refresh Interval</label>
                    <Select
                        key={`refresh-select-${settingsLoaded}-${settings.sportTickerRefresh}`}
                        selectedValue={secondsToValueString(settings.sportTickerRefresh)}
                        name="refreshInterval"
                        options={refreshPresets}
                        caretIcon={<CaretIcon />}
                        modalCloseButton={<ModalCloseButton />}
                        onChange={handleRefreshChange}
                    />
                </div>

                {/* Scroll Speed (only show if not single team mode) */}
                {!isSingleTeamMode && (
                    <div style={{ flex: 1, minWidth: "150px" }}>
                        <label className="setting-label">Scroll Speed</label>
                        <Select
                            key={`scroll-select-${settingsLoaded}-${scrollSpeed}`}
                            selectedValue={getScrollSpeedValue(scrollSpeed)}
                            name="scrollSpeed"
                            options={scrollSpeedPresets}
                            caretIcon={<CaretIcon />}
                            modalCloseButton={<ModalCloseButton />}
                            onChange={handleScrollSpeedChange}
                        />
                    </div>
                )}

                {/* Dark Mode Toggle */}
                <div style={{ minWidth: "120px" }}>
                    <FormControlLabel
                        control={
                            <Switch
                                checked={settings.sportDarkMode}
                                onChange={handleDarkModeChange}
                                color="primary"
                            />
                        }
                        label="Dark Mode"
                        style={{ color: "#fff", marginLeft: 0 }}
                    />
                </div>
            </div>

            {/* Start Button */}
            <Button
                variant="contained"
                onClick={isTickerRunning ? handleStopTicker : handleStartTicker}
                disabled={isLoading || (mode === "league" ? !selectedLeague : favoriteTeams.length === 0)}
                className={`start-button ${isTickerRunning ? "stop" : ""}`}
            >
                {isLoading ? (
                    <CircularProgress size={24} style={{ color: "#fff", marginRight: 8 }} />
                ) : isTickerRunning ? (
                    <Stop />
                ) : (
                    <PlayArrow />
                )}
                {isLoading ? "PLEASE WAIT..." : isTickerRunning ? "STOP SPORTS TICKER" : "START SPORTS TICKER"}
            </Button>
        </div>
    );
};

export default SportsTickerWidget;
