import { Button, FormControlLabel, Switch } from "@material-ui/core";
import React, { useEffect, useState, ReactElement } from "react";
import toast from "react-hot-toast";
import { FontList } from "../../const/fonts";
import { readDeviceInfo } from "../../services";
import { DeviceInfo } from "../../types/DeviceInfo.type";
import {
  readSettings,
  toggleMarqueeOverlays,
  toggleVideoLoopMode,
  toggleFuzzyMatch,
  updateFuzzyMatchDistance,
  toggleTextOnMainPanel,
  toggleVideoMarquees,
  updateDeviceFont,
  updatePixelcadeDotTypeSPI,
  updatePixelcadeMiniDisplayTypeI2C,
  updatePixelcadeDeviceOLEDMode,
  // toggleMiniOLEDGlitchMode, // Not enabled yet
  updateDeviceName,
} from "../../services/settings";
import { Select, CaretIcon, ModalCloseButton } from "react-responsive-select";

import "react-responsive-select/dist/react-responsive-select.css";

type SettingType = {
  font: string;
  SubDisplayScrollingOnly: boolean;
  ImageMarqueesOnly: boolean;
  SubDisplayNumModules: number;
  FontColor: string;
  marqueeOverlays: boolean;
  videoLoopMode: boolean;
  fuzzyMatch: boolean;
  fuzzyMatchDistance: number;
  PixelcadeDotType: string;
  PixelcadeMiniDisplayType: string;
  PixelcadeOLEDMode: string;
  PixelcadeOLEDGlitch: boolean;
};

// WiFi types
interface WiFiStatus {
  connected: boolean;
  ssid: string;
  macAddress: string;
  ipAddress: string;
  hasInternet: boolean;
  signalStrength: number;
}

interface WiFiNetwork {
  ssid: string;
  signal: number;
  security: string;
  inUse: boolean;
}

// USB Storage types
interface USBStorageStatus {
  usbDetected: boolean;
  usbMounted: boolean;
  usbDevice: string;
  usbLabel: string;
  usbMountPoint: string;
  usbFileSystem: string;
  usbFreeBytes: number;
  usbTotalBytes: number;
  hasLCDFolder: boolean;
  hasMP4Folder: boolean;
  readyForUse: boolean;
  canFormat: boolean;
  errorMessage: string;
}

const DeviceSettings = (): ReactElement => {
  const [deviceName, setDeviceName] = useState<string>("");
  const [settings, setSettings] = useState<SettingType | undefined>();
  const [deviceInfo, setDeviceInfo] = useState<DeviceInfo>();

  // WiFi state
  const [wifiStatus, setWifiStatus] = useState<WiFiStatus | null>(null);
  const [wifiNetworks, setWifiNetworks] = useState<WiFiNetwork[]>([]);
  const [wifiScanning, setWifiScanning] = useState<boolean>(false);
  const [wifiConnecting, setWifiConnecting] = useState<boolean>(false);
  const [selectedNetwork, setSelectedNetwork] = useState<string>("");
  const [wifiPassword, setWifiPassword] = useState<string>("");
  const [showWifiPassword, setShowWifiPassword] = useState<boolean>(false);
  const [showNetworkList, setShowNetworkList] = useState<boolean>(false);

  // USB Storage state
  const [usbStatus, setUsbStatus] = useState<USBStorageStatus | null>(null);
  const [usbFormatting, setUsbFormatting] = useState<boolean>(false);

  const hostname = window.location.hostname;

  // Fetch WiFi status
  const fetchWifiStatus = async () => {
    try {
      const response = await fetch(`http://${hostname}:8080/v2/wifi/status`);
      if (response.ok) {
        const data = await response.json();
        setWifiStatus(data);
      }
    } catch (error) {
      console.error("Error fetching WiFi status:", error);
    }
  };

  // Scan for WiFi networks
  const scanWifiNetworks = async () => {
    setWifiScanning(true);
    try {
      const response = await fetch(`http://${hostname}:8080/v2/wifi/scan`);
      if (response.ok) {
        const data = await response.json();
        setWifiNetworks(data.networks || []);
      }
    } catch (error) {
      console.error("Error scanning WiFi networks:", error);
    } finally {
      setWifiScanning(false);
    }
  };

  // Connect to WiFi network
  const connectToWifi = async () => {
    if (!selectedNetwork) {
      toast.error("Please select a network");
      return;
    }

    setWifiConnecting(true);
    try {
      const response = await fetch(`http://${hostname}:8080/v2/wifi/connect`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ ssid: selectedNetwork, password: wifiPassword }),
      });

      const data = await response.json();

      if (data.success) {
        toast.success(data.message);
        setShowNetworkList(false);
        setSelectedNetwork("");
        setWifiPassword("");
        // Refresh status after connection
        setTimeout(fetchWifiStatus, 2000);
      } else {
        toast.error(data.message || "Connection failed");
      }
    } catch (error) {
      toast.error("Failed to connect to network");
    } finally {
      setWifiConnecting(false);
    }
  };

  // Fetch USB storage status
  const fetchUsbStatus = async () => {
    try {
      const response = await fetch(`http://${hostname}:8080/v2/usb/status`);
      if (response.ok) {
        const data = await response.json();
        setUsbStatus(data);
      }
    } catch (error) {
      console.error("Error fetching USB status:", error);
    }
  };

  // Format USB drive
  const formatUsbDrive = async () => {
    if (!usbStatus?.usbDevice) {
      toast.error("No USB device detected");
      return;
    }

    if (!window.confirm("This will ERASE ALL DATA on the USB drive and format it for Pixelcade use. Continue?")) {
      return;
    }

    setUsbFormatting(true);
    try {
      const response = await fetch(`http://${hostname}:8080/v2/usb/format`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ device: usbStatus.usbDevice, label: "PixelUSB" }),
      });

      const data = await response.json();

      if (data.success) {
        toast.success(data.message);
        // Refresh status after format
        setTimeout(fetchUsbStatus, 2000);
      } else {
        toast.error(data.message || "Format failed");
      }
    } catch (error) {
      toast.error("Failed to format USB drive");
    } finally {
      setUsbFormatting(false);
    }
  };

  // Helper to format bytes
  const formatBytes = (bytes: number): string => {
    if (bytes === 0) return "0 B";
    const k = 1024;
    const sizes = ["B", "KB", "MB", "GB", "TB"];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(1)) + " " + sizes[i];
  };

  useEffect(() => {
    readDeviceInfo().then((data) => {
      setDeviceName(data.name);
      setDeviceInfo(data);
    });
    readSettings()
      .then((data) => {
        setSettings(data);
      })
      .catch();

    // Load WiFi status
    fetchWifiStatus();
    // Load USB status
    fetchUsbStatus();
  }, []);


    const getRebootMessage = (message: string) => {
        return (
            <div>
                <p>{message}</p>
            </div>
        );
    };
    const updateImageMarqueesOnly = () => {
        if (settings) {
            toast.promise(
                toggleVideoMarquees(!settings.ImageMarqueesOnly),
                {
                    loading: "Updating video marquees settings",
                    success: () => {
                        setSettings({
                            ...settings,
                            ImageMarqueesOnly: !settings.ImageMarqueesOnly,
                        });
                        return getRebootMessage(
                            `Video marquees settings updated`
                        );
                    },
                    error: "Setting failed",
                },
                { duration: 4000 }
            );
        }
    };

    // OLED Glitch Mode - not enabled yet, commented out
    // const updateMiniOLEDGlitchMode = () => {
    //   if (settings) {
    //       toast.promise(
    //         toggleMiniOLEDGlitchMode(!settings.PixelcadeOLEDGlitch),
    //           {
    //               loading: "Updating Mini OLED Glitch Mode",
    //               success: () => {
    //                   setSettings({
    //                       ...settings,
    //                       PixelcadeOLEDGlitch: !settings.PixelcadeOLEDGlitch,
    //                   });
    //                   return getRebootMessage(
    //                       `Mini OLED Glitch Mode Updated`
    //                   );
    //               },
    //               error: "Mini OLED Glitch Mode Update Failed",
    //           },
    //           { duration: 4000 }
    //       );
    //   }
    // };
    

    const updateTextOnMainPanel = () => {
        if (settings) {
            toast.promise(
                toggleTextOnMainPanel(!settings.SubDisplayScrollingOnly),
                {
                    loading: "Updating Text scrolling settings",
                    success: () => {
                        setSettings({
                            ...settings,
                            SubDisplayScrollingOnly:
                                !settings.SubDisplayScrollingOnly,
                        });
                        return getRebootMessage(
                            `Text scrolling settings updated`
                        );
                    },
                    error: "Setting failed",
                },
                { duration: 4000 }
            );
        }
    };

    const updateName = () => {
        toast.promise(
            updateDeviceName(deviceName),
            {
                loading: "Updating Device Name",
                success: () => {
                    return getRebootMessage(`Device Name updated`);
                },
                error: "Setting failed",
            },
            { duration: 4000 }
        );
    };

    const updateFont = () => {
        if (settings) {
            toast.promise(
                updateDeviceFont(settings.font),
                {
                    loading: "Updating Device Font",
                    success: "Font updated",
                    error: "Setting failed",
                },
                { duration: 4000 }
            );
        }
    };

   const updatePixelcadeDotType = () => {
      if (settings) {
          toast.promise(
              updatePixelcadeDotTypeSPI(settings.PixelcadeDotType),
              {
                  loading: "Updating Pixelcade Sub-Display Type",
                  success: "Sub-Display Type updated",
                  error: "Sub-Display Type Updated Failed",
              },
              { duration: 4000 }
          );
      }
  }; 

  const updatePixelcadeMiniDisplayType = () => {
    if (settings) {
        toast.promise(
          updatePixelcadeMiniDisplayTypeI2C(settings.PixelcadeMiniDisplayType),
            {
                loading: "Updating Pixelcade Mini Display Type",
                success: "Pixelcade Mini Display Type Updated",
                error: "Pixelcade Mini Display Updated Failed",
            },
            { duration: 4000 }
        );
    }
}; 

const updatePixelcadeOLEDMode = () => {
  if (settings) {
      toast.promise(
        updatePixelcadeDeviceOLEDMode(settings.PixelcadeOLEDMode),
          {
              loading: "Updating Pixelcade Mini OLED Mode",
              success: "Pixelcade Mini OLED Mode Updated",
              error: "Pixelcade Mini OLED Mode Update Failed",
          },
          { duration: 4000 }
      );
  }
}; 

  const getFonts = () => {
    return FontList.map((font) => {
      return {
        value: font,
        text: font,
      };
    });
  };

  const updateMarqueeOverlay = () => {
    if (settings) {
      toast.promise(
        toggleMarqueeOverlays(!settings.marqueeOverlays),
        {
          loading: "Updating Marquee Overlay settings",
          success: () => {
            setSettings({
              ...settings,
              marqueeOverlays: !settings.marqueeOverlays,
            });
            return getRebootMessage(`Marquee Overlay settings updated`);
          },
          error: "Setting failed",
        },
        { duration: 4000 }
      );
    }
  };

  const updateVideoLoopMode = () => {
    if (settings) {
      toast.promise(
        toggleVideoLoopMode(!settings.videoLoopMode),
        {
          loading: "Updating Video Loop Mode",
          success: () => {
            setSettings({
              ...settings,
              videoLoopMode: !settings.videoLoopMode,
            });
            return getRebootMessage(`Video Loop Mode ${!settings.videoLoopMode ? 'enabled' : 'disabled'}`);
          },
          error: "Setting failed",
        },
        { duration: 4000 }
      );
    }
  };

  const updateFuzzyMatch = () => {
    if (settings) {
      toast.promise(
        toggleFuzzyMatch(!settings.fuzzyMatch),
        {
          loading: "Updating Fuzzy Matching settings",
          success: () => {
            setSettings({
              ...settings,
              fuzzyMatch: !settings.fuzzyMatch,
            });
            return getRebootMessage(`Fuzzy Matching settings updated`);
          },
          error: "Setting failed",
        },
        { duration: 4000 }
      );
    }
  };

  const handleFuzzyMatchDistanceChange = (newDistance: number) => {
    if (settings) {
      toast.promise(
        updateFuzzyMatchDistance(newDistance),
        {
          loading: "Updating Fuzzy Match Distance",
          success: () => {
            setSettings({
              ...settings,
              fuzzyMatchDistance: newDistance,
            });
            return `Fuzzy Match Distance set to ${newDistance}`;
          },
          error: "Setting failed",
        },
        { duration: 4000 }
      );
    }
  };

  // Helper to get signal strength indicator
  const getSignalIcon = (signal: number): string => {
    if (signal >= 70) return "";
    if (signal >= 50) return "";
    if (signal >= 30) return "";
    return "";
  };

  return (
    <div className="fade-in">
      {settings && (
        <div className="layout-grid" style={{ marginTop: "0.5rem", gap: "2rem" }}>
          {/* Device Name Section */}
          <div className="settings">
            <label style={{ margin: "1rem 0 .5rem 0" }}>Device Name</label>
            <input
              style={{
                height: 36,
                fontSize: "1.2rem",
                padding: "0 0.4rem",
              }}
              type="text"
              id="fname"
              name="fname"
              value={deviceName}
              onChange={(event) => {
                setDeviceName(event.target.value);
              }}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updateName()}
              >
                Update
              </Button>
            </div>
          </div>

          {/* Device Info Section */}
        <div className="settings">
          <label style={{ margin: "1rem 0 .5rem 0", fontWeight: "bold" }}>Device Info</label>
          {deviceInfo && (
            <div style={{ fontSize: "1rem", lineHeight: "1.6" }}>
              <p><strong>Name:</strong> <span style={{ color: "#666", fontWeight: "normal" }}>{deviceInfo.name}</span></p>
              <p><strong>Hostname:</strong> <span style={{ color: "#666", fontWeight: "normal" }}>{deviceInfo.hostname}</span></p>
              <p><strong>Firmware:</strong> <span style={{ color: "#666", fontWeight: "normal" }}>v{deviceInfo.firmwareVersion}</span></p>
              <p><strong>Artwork Version:</strong> <span style={{ color: "#666", fontWeight: "normal" }}>{deviceInfo.artworkVersion}</span></p>
              <p><strong>Resolution:</strong> <span style={{ color: "#666", fontWeight: "normal" }}>{deviceInfo.resolution.width}x{deviceInfo.resolution.height}</span></p>
              <p><strong>Generation:</strong> <span style={{ color: "#666", fontWeight: "normal" }}>{deviceInfo.pixelcadeGen}</span></p>
              <p><strong>Connected Sub-Displays:</strong></p>
              <ul style={{ marginLeft: "1.5rem", marginTop: "0.5rem", color: "#666", fontWeight: "normal" }}>
                {deviceInfo.oled && <li>OLED Display</li>}
                {deviceInfo.miniLCD && <li>Mini LCD</li>}
                {deviceInfo.dotMatrix && <li>Dot Matrix</li>}
                {deviceInfo.sevenSegment && <li>Seven Segment</li>}
              </ul>
              <p><strong>Network Status:</strong> <span style={{ color: deviceInfo.hasJoinedNetwork ? "green" : "red", fontWeight: "normal" }}>
                {deviceInfo.hasJoinedNetwork ? "Connected" : "Not Connected"}
              </span></p>
              <p><strong>Pairing Status:</strong> <span style={{ color: deviceInfo.isPaired ? "green" : "red", fontWeight: "normal" }}>
                {deviceInfo.isPaired ? "Paired" : "Not Paired"}
              </span></p>
            </div>
          )}
        </div>

          {/* WiFi Setup Section */}
          <div className="settings">
            <label style={{ margin: "1rem 0 .5rem 0", fontWeight: "bold" }}>WiFi Setup</label>
            <p style={{ marginBottom: "1rem" }}>
              Connect to your home WiFi to enable widgets, updates, and other features that require internet access.
            </p>

            {/* Connection Status */}
            {wifiStatus && (
              <div style={{
                marginBottom: "1rem",
                padding: "0.75rem",
                background: wifiStatus.connected ? (wifiStatus.hasInternet ? "#e8f5e9" : "#fff3e0") : "#ffebee",
                borderRadius: "4px",
                border: `1px solid ${wifiStatus.connected ? (wifiStatus.hasInternet ? "#4caf50" : "#ff9800") : "#f44336"}`
              }}>
                <p style={{ margin: 0, fontWeight: "bold", color: wifiStatus.connected ? (wifiStatus.hasInternet ? "#2e7d32" : "#e65100") : "#c62828" }}>
                  {wifiStatus.connected
                    ? (wifiStatus.hasInternet ? " Connected with Internet" : " Connected (No Internet)")
                    : " Not Connected"}
                </p>
                {wifiStatus.connected && (
                  <>
                    <p style={{ margin: "0.5rem 0 0 0", fontSize: "0.9rem" }}>
                      <strong>Network:</strong> {wifiStatus.ssid}
                    </p>
                    <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.9rem" }}>
                      <strong>IP Address:</strong> {wifiStatus.ipAddress}
                    </p>
                    <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.9rem" }}>
                      <strong>Signal:</strong> {getSignalIcon(wifiStatus.signalStrength)} ({wifiStatus.signalStrength}%)
                    </p>
                  </>
                )}
              </div>
            )}

            {/* MAC Address Display */}
            {wifiStatus && (
              <p style={{ margin: "0 0 1rem 0", fontSize: "0.9rem" }}>
                <strong>MAC Address:</strong> <span style={{ fontFamily: "monospace", color: "#666" }}>{wifiStatus.macAddress || "Unknown"}</span>
              </p>
            )}

            {/* Change Network / Connect Button */}
            {!showNetworkList ? (
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  setShowNetworkList(true);
                  scanWifiNetworks();
                }}
                style={{ marginBottom: "1rem" }}
              >
                {wifiStatus?.connected ? "Change WiFi Network" : "Connect to WiFi"}
              </Button>
            ) : (
              <div style={{ marginBottom: "1rem" }}>
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: "0.5rem" }}>
                  <label style={{ fontWeight: "bold" }}>Available Networks</label>
                  <div>
                    <Button
                      variant="outlined"
                      size="small"
                      onClick={scanWifiNetworks}
                      disabled={wifiScanning}
                      style={{ marginRight: "0.5rem" }}
                    >
                      {wifiScanning ? "Scanning..." : "Refresh"}
                    </Button>
                    <Button
                      variant="outlined"
                      size="small"
                      onClick={() => {
                        setShowNetworkList(false);
                        setSelectedNetwork("");
                        setWifiPassword("");
                      }}
                    >
                      Cancel
                    </Button>
                  </div>
                </div>

                {/* Network List */}
                <div style={{
                  maxHeight: "200px",
                  overflowY: "auto",
                  border: "1px solid #ddd",
                  borderRadius: "4px",
                  marginBottom: "1rem"
                }}>
                  {wifiScanning ? (
                    <p style={{ padding: "1rem", textAlign: "center", color: "#666" }}>Scanning for networks...</p>
                  ) : wifiNetworks.length === 0 ? (
                    <p style={{ padding: "1rem", textAlign: "center", color: "#666" }}>No networks found</p>
                  ) : (
                    wifiNetworks.map((network, index) => (
                      <div
                        key={`${network.ssid}-${index}`}
                        onClick={() => setSelectedNetwork(network.ssid)}
                        style={{
                          padding: "0.75rem",
                          cursor: "pointer",
                          background: selectedNetwork === network.ssid ? "#e3f2fd" : (index % 2 === 0 ? "#fafafa" : "#fff"),
                          borderBottom: index < wifiNetworks.length - 1 ? "1px solid #eee" : "none",
                          display: "flex",
                          justifyContent: "space-between",
                          alignItems: "center"
                        }}
                      >
                        <span>
                          {network.inUse && <span style={{ color: "#4caf50", marginRight: "0.5rem" }}></span>}
                          {network.ssid}
                        </span>
                        <span style={{ color: "#666", fontSize: "0.85rem" }}>
                          {getSignalIcon(network.signal)} {network.security !== "" && ""}
                        </span>
                      </div>
                    ))
                  )}
                </div>

                {/* Password Input */}
                {selectedNetwork && (
                  <div style={{ marginBottom: "1rem" }}>
                    <label style={{ display: "block", marginBottom: "0.5rem", color: "#666" }}>
                      Password for "{selectedNetwork}"
                    </label>
                    <div style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                      <input
                        style={{
                          height: 36,
                          fontSize: "1rem",
                          padding: "0 0.6rem",
                          width: "100%",
                          maxWidth: "250px",
                        }}
                        type={showWifiPassword ? "text" : "password"}
                        placeholder="Enter WiFi password"
                        value={wifiPassword}
                        onChange={(e) => setWifiPassword(e.target.value)}
                      />
                      <Button
                        variant="outlined"
                        size="small"
                        onClick={() => setShowWifiPassword(!showWifiPassword)}
                        style={{ minWidth: "80px" }}
                      >
                        {showWifiPassword ? "Hide" : "Show"}
                      </Button>
                    </div>
                  </div>
                )}

                {/* Connect Button */}
                {selectedNetwork && (
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={connectToWifi}
                    disabled={wifiConnecting}
                  >
                    {wifiConnecting ? "Connecting..." : `Connect to ${selectedNetwork}`}
                  </Button>
                )}
              </div>
            )}

            {/* Refresh Status Button */}
            <Button
              variant="outlined"
              size="small"
              onClick={fetchWifiStatus}
              style={{ marginTop: showNetworkList ? "0" : "0.5rem" }}
            >
              Refresh Status
            </Button>
          </div>

          {/* USB Storage Section */}
          <div className="settings">
            <label style={{ margin: "1rem 0 .5rem 0", fontWeight: "bold" }}>USB Storage</label>
            <p style={{ marginBottom: "1rem" }}>
              Plug in a USB drive to expand your marquee storage. USB storage takes priority over internal storage.
            </p>

            {usbStatus ? (
              <>
                {/* USB Status Display */}
                <div style={{
                  marginBottom: "1rem",
                  padding: "0.75rem",
                  background: usbStatus.readyForUse ? "#e8f5e9" : (usbStatus.usbDetected ? "#fff3e0" : "#f5f5f5"),
                  borderRadius: "4px",
                  border: `1px solid ${usbStatus.readyForUse ? "#4caf50" : (usbStatus.usbDetected ? "#ff9800" : "#ddd")}`
                }}>
                  {usbStatus.readyForUse ? (
                    <>
                      <p style={{ margin: 0, fontWeight: "bold", color: "#2e7d32" }}>
                         USB Storage Ready
                      </p>
                      <p style={{ margin: "0.5rem 0 0 0", fontSize: "0.9rem" }}>
                        <strong>Label:</strong> {usbStatus.usbLabel || "PixelUSB"}
                      </p>
                      <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.9rem" }}>
                        <strong>Free Space:</strong> {formatBytes(usbStatus.usbFreeBytes)} / {formatBytes(usbStatus.usbTotalBytes)}
                      </p>
                      <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.9rem" }}>
                        <strong>Mount Point:</strong> <span style={{ fontFamily: "monospace" }}>{usbStatus.usbMountPoint}</span>
                      </p>
                    </>
                  ) : usbStatus.usbMounted ? (
                    <>
                      <p style={{ margin: 0, fontWeight: "bold", color: "#e65100" }}>
                         USB Mounted (Missing Folders)
                      </p>
                      <p style={{ margin: "0.5rem 0 0 0", fontSize: "0.9rem" }}>
                        The USB drive is mounted but missing required folders.
                      </p>
                      <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.9rem", color: "#666" }}>
                        {!usbStatus.hasLCDFolder && " Missing lcdmarquees folder"}
                        {!usbStatus.hasLCDFolder && !usbStatus.hasMP4Folder && <br />}
                        {!usbStatus.hasMP4Folder && " Missing mp4marquees folder"}
                      </p>
                    </>
                  ) : usbStatus.usbDetected ? (
                    <>
                      <p style={{ margin: 0, fontWeight: "bold", color: "#e65100" }}>
                         USB Detected (Not Mounted)
                      </p>
                      <p style={{ margin: "0.5rem 0 0 0", fontSize: "0.9rem" }}>
                        <strong>Device:</strong> <span style={{ fontFamily: "monospace" }}>{usbStatus.usbDevice}</span>
                      </p>
                      <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.9rem", color: "#666" }}>
                        This USB drive can be formatted for Pixelcade use.
                      </p>
                    </>
                  ) : (
                    <p style={{ margin: 0, color: "#666" }}>
                      No USB drive detected. Plug in a USB drive to use external storage.
                    </p>
                  )}
                </div>

                {/* Format Button - only show if USB detected but not ready */}
                {usbStatus.canFormat && (
                  <Button
                    variant="contained"
                    color="secondary"
                    onClick={formatUsbDrive}
                    disabled={usbFormatting}
                    style={{ marginBottom: "1rem" }}
                  >
                    {usbFormatting ? "Formatting..." : "Format USB for Pixelcade"}
                  </Button>
                )}

                {/* Refresh Button */}
                <Button
                  variant="outlined"
                  size="small"
                  onClick={fetchUsbStatus}
                >
                  Refresh Status
                </Button>
              </>
            ) : (
              <p style={{ color: "#666" }}>Loading USB status...</p>
            )}
          </div>

          {/* Rest of the existing sections stay exactly the same */}
          <div className="settings">
            <p>
              When disabled, Pixelcade will not show video marquees and will show static marquees only.
            </p>
            <FormControlLabel
              style={{ marginTop: "1.2rem" }}
              control={
                <Switch
                  checked={!settings.ImageMarqueesOnly}
                  onChange={() => updateImageMarqueesOnly()}
                  color="primary"
                />
              }
              label="Show Video Marquees"
            />
          </div>

          {/* Video Loop Mode Setting */}
          <div className="settings" style={{ marginTop: "2rem" }}>
            <p>
              When enabled, video marquees will continue to loop indefinitely and no longer land on the static marquee.
            </p>
            <FormControlLabel
              style={{ marginTop: "1.2rem" }}
              control={
                <Switch
                  checked={settings.videoLoopMode}
                  onChange={() => updateVideoLoopMode()}
                  color="primary"
                />
              }
              label="Loop Video Marquees Indefinitely"
            />
          </div>

          {/* Splash Screens Setting */}
          <div className="settings" style={{ marginTop: "2rem" }}>
            <p>
              Disabling splash screens means Pixelcade will not display now playing splash screens, high scores, or marquee overlays. <br></br><br></br>Only a Pixelcade LED marquee
              (if connected) or a Pixelcade Dot sub-display (if connected) will
              display scrolling text for now playing game launches and high scores.
            </p>
            <FormControlLabel
              style={{ marginTop: "1.2rem" }}
              control={
                <Switch
                  checked={settings.SubDisplayScrollingOnly}
                  onChange={() => updateTextOnMainPanel()}
                  color="primary"
                />
              }
              label="Disable Splash Screens"
            />
          </div>
  
          {/* Font Settings */}
          <div className="settings" style={{ marginTop: "2rem" }}>
            <label style={{ margin: ".5rem 0 .5rem 0" }}>Font</label>
            <Select
              selectedValue={settings.font}
              name="font"
              options={getFonts()}
              caretIcon={<CaretIcon />}
              modalCloseButton={<ModalCloseButton />}
              onChange={(changes: any) => {
                const font = changes.value;
                setSettings({ ...settings, font });
              }}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updateFont()}
              >
                Update
              </Button>
            </div>
          </div>
  
          {/* Marquee Overlays Setting */}
          <div className="settings" style={{ marginTop: "2rem" }}>
            <p>
              When disabled, Pixelcade will not show overlays such as high
              scores in a small bar on the bottom of game marquees.
            </p>
            <FormControlLabel
              style={{ marginTop: "1.2rem" }}
              control={
                <Switch
                  checked={settings.marqueeOverlays}
                  onChange={() => updateMarqueeOverlay()}
                  color="primary"
                />
              }
              label="Show Marquee Overlays"
            />
          </div>

          {/* Fuzzy Matching Setting */}
          <div className="settings" style={{ marginTop: "2rem" }}>
            <p>
              When enabled, will attempt to match artwork when there is not an exact file match. Ex. mame-game.jpg will match with mame-gamex(USA).jpg
            </p>
            <FormControlLabel
              style={{ marginTop: "1.2rem" }}
              control={
                <Switch
                  checked={settings.fuzzyMatch}
                  onChange={() => updateFuzzyMatch()}
                  color="primary"
                />
              }
              label="Fuzzy Matching"
            />
            {settings.fuzzyMatch && (
              <div style={{ marginTop: "1.5rem", paddingLeft: "1rem" }}>
                <label style={{ display: "block", marginBottom: "0.5rem", color: "#666" }}>
                  Fuzzy Match Distance (0-10): {settings.fuzzyMatchDistance}
                </label>
                <input
                  type="range"
                  min="0"
                  max="10"
                  value={settings.fuzzyMatchDistance}
                  onChange={(e) => handleFuzzyMatchDistanceChange(Number(e.target.value))}
                  style={{ width: "100%", maxWidth: "300px" }}
                />
                <p style={{ fontSize: "0.9rem", color: "#666", marginTop: "0.5rem" }}>
                  Lower values require closer matches. Higher values allow more variation.
                </p>
              </div>
            )}
          </div>

          {/* Dot Sub-Display Type Setting */}
          <div className="settings" style={{ marginTop: "2rem" }}>
            <label style={{ margin: ".5rem 0 .5rem 0" }}>Pixelcade Dot Sub-Display Type</label>
            <label style={{ margin: ".5rem 0 .5rem 0", display: "block", color: "#666" }}>Please Select Manually</label>
            <Select
              selectedValue={settings.PixelcadeDotType}
              name="PixelcadeDotType"
              options={[
                { text: "Matrix", value: "matrix" },
                { text: "7 Segment", value: "7segment" },
                { text: "None", value: "none" }
              ]}
              caretIcon={<CaretIcon />}
              modalCloseButton={<ModalCloseButton />}
              onChange={(changes: any) => {
                const PixelcadeDotType = changes.value;
                setSettings({ ...settings, PixelcadeDotType });
              }}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updatePixelcadeDotType()}
              >
                Update
              </Button>
            </div>
          </div>
  
          {/* Mini Dot Sub-Display Type Setting */}
          <div className="settings" style={{ marginTop: "2rem" }}>
            <label style={{ margin: ".5rem 0 .5rem 0" }}>Pixelcade Mini Dot Sub-Display Type</label>
            <label style={{ margin: ".5rem 0 .5rem 0", display: "block", color: "#666" }}>Auto Detected</label>
            <Select
              selectedValue={settings.PixelcadeMiniDisplayType}
              name="PixelcadeMiniDisplayType"
              options={[
                { text: "Mini OLED", value: "oled" },
                { text: "Mini LCD 4 Lines", value: "lcd4line" },
                { text: "Mini LCD 2 Lines", value: "lcd2line" },
                { text: "None", value: "none" }
              ]}
              caretIcon={<CaretIcon />}
              modalCloseButton={<ModalCloseButton />}
              onChange={(changes: any) => {
                const PixelcadeMiniDisplayType = changes.value;
                setSettings({ ...settings, PixelcadeMiniDisplayType });
              }}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updatePixelcadeMiniDisplayType()}
              >
                Update
              </Button>
            </div>
          </div>
  
          {/* OLED Mode Setting */}
          <div className="settings" style={{ marginTop: "2rem" }}>
            <label style={{ margin: ".5rem 0 .5rem 0" }}>Pixelcade Mini OLED Mode</label>
            <Select
              selectedValue={settings.PixelcadeOLEDMode}
              name="PixelcadeOLEDMode"
              options={[
                { text: "Marquee Mode", value: "marquee" },
                { text: "Metadata Mode", value: "metadata" }
              ]}
              caretIcon={<CaretIcon />}
              modalCloseButton={<ModalCloseButton />}
              onChange={(changes: any) => {
                const PixelcadeOLEDMode = changes.value;
                setSettings({ ...settings, PixelcadeOLEDMode });
              }}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 2fr",
                gridGap: "1rem",
              }}
            >
              <div />
              <Button
                style={{ marginTop: "1.2rem" }}
                variant="contained"
                color="primary"
                onClick={() => updatePixelcadeOLEDMode()}
              >
                Update
              </Button>
            </div>
          </div>
  
          {/* OLED Glitch Mode Setting - Not enabled yet, commented out
          <div className="settings" style={{ marginTop: "2rem" }}>
            <p>
              Pixelcade Mini OLED Glitch Mode
            </p>
            <FormControlLabel
              style={{ marginTop: "1.2rem" }}
              control={
                <Switch
                  checked={settings.PixelcadeOLEDGlitch}
                  onChange={() => updateMiniOLEDGlitchMode()}
                  color="primary"
                />
              }
              label="Enable Pixelcade Mini OLED Glitch Mode"
            />
          </div>
          */}

        </div>
      )}

    </div>
  );

};

export default DeviceSettings;
