import React, { useEffect, useState } from "react";
import { Button } from "@material-ui/core";
import toast from "react-hot-toast";
import { toggleDebugMode, readSettings } from "../../services/settings";
import "./styles.css";

const LogViewer: React.FC = () => {
  const [logContent, setLogContent] = useState<string>("");
  const [autoScroll, setAutoScroll] = useState<boolean>(true);
  const [debugMode, setDebugMode] = useState<boolean>(false);
  const hostname = localStorage.getItem("hostname") || window.location.hostname;

  // Fetch current debug mode setting
  useEffect(() => {
    readSettings()
      .then((data) => {
        setDebugMode(data.debug || false);
      })
      .catch(() => {
        // Settings not available, keep default
      });
  }, []);

  const handleDebugToggle = () => {
    const newValue = !debugMode;
    toast.promise(
      toggleDebugMode(newValue),
      {
        loading: "Updating Debug Mode",
        success: () => {
          setDebugMode(newValue);
          return `Debug Mode ${newValue ? 'enabled' : 'disabled'}`;
        },
        error: "Failed to update debug mode",
      },
      { duration: 4000 }
    );
  };

  useEffect(() => {
    const fetchLogFile = async () => {
      try {
        const response = await fetch(`http://${hostname}:8080/v2/log`);
        const text = await response.text();
        setLogContent(text);
      } catch (error) {
        setLogContent("Error loading log file: " + (error instanceof Error ? error.message : String(error)));
      }
    };

    fetchLogFile();

    // Auto-refresh every 5 seconds
    const interval = setInterval(fetchLogFile, 5000);

    return () => clearInterval(interval);
  }, [hostname]);

  useEffect(() => {
    if (autoScroll) {
      const logContainer = document.getElementById("log-content");
      if (logContainer) {
        logContainer.scrollTop = logContainer.scrollHeight;
      }
    }
  }, [logContent, autoScroll]);

  const handleDownload = () => {
    const blob = new Blob([logContent], { type: "text/plain" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "pixelcadelcd.log";
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  return (
    <div className="log-viewer-container fade-in">
      <div className="log-header">
        <h2>Pixelcade LCD Log</h2>
        <span className="log-subtitle">Auto-updates every 5 seconds</span>
      </div>

      <div className="log-controls">
        <div className="log-checkboxes">
          <label className="auto-scroll-toggle">
            <input
              type="checkbox"
              checked={autoScroll}
              onChange={(e) => setAutoScroll(e.target.checked)}
            />
            Auto-scroll to bottom
          </label>
          <label className="auto-scroll-toggle">
            <input
              type="checkbox"
              checked={debugMode}
              onChange={handleDebugToggle}
            />
            Debug logging
          </label>
        </div>
        <div className="log-buttons">
          <Button
            variant="contained"
            color="primary"
            onClick={handleDownload}
            disabled={!logContent}
            size="small"
          >
            Download Log
          </Button>
        </div>
      </div>

      <div className="log-content-wrapper">
        <pre id="log-content" className="log-content">
          {logContent || "Loading log..."}
        </pre>
      </div>
    </div>
  );
};

export default LogViewer;
