import axios from "axios";
import { getDeviceUrl } from ".";

// Start the clock widget
export const startClock = (
    face: string = "cyberpunk",
    use12Hour: boolean = true,
    showSeconds: boolean = true
): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/clock/start?face=${face}&use12Hour=${use12Hour}&showSeconds=${showSeconds}`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Stop the clock widget
export const stopClock = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/clock/stop`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Get clock status
export const getClockStatus = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/clock/status`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Get available clock faces
export const getClockFaces = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/clock/faces`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Update a clock setting
export const updateClockSetting = (key: string, value: string): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/settings?key=${key}&value=${encodeURIComponent(value)}`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};
