import { FormControlLabel, Switch, Button } from "@material-ui/core";
import { CloudUpload } from "@material-ui/icons";
import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { useAppSettings } from "../../App";
import { useDebounce } from "../../hooks/useDebounce";
import { readFilesystemMarquees } from "../../services";
import { emptyMarquee, MarqueeInfo } from "../../types/DeviceInfo.type";
import Loading from "../Loading";
import MarqueeItem from "../MarqueeItem";

const MarqueeList: React.FC = () => {
    const [loading, setLoading] = useState<boolean>(false);
    const [marqueeData, setMarqueeData] = useState<MarqueeInfo[]>([]);
    const { system } = useParams();
    const [searchTerm, setSearchTerm] = useState<string>("");
    const debouncedSearchTerm = useDebounce(searchTerm, 350);
    const { streamImages, setStreamImages } = useAppSettings();
    const navigate = useNavigate();

    useEffect(() => {
        if (system) {
            setLoading(true);

            // Fetch marquees from filesystem only
            readFilesystemMarquees(system)
                .then((data) => {
                    const marquees = data.marquees || [];
                    setMarqueeData(marquees);
                })
                .catch((error) => {
                    console.error("Error fetching marquees:", error);
                    setMarqueeData([]);
                })
                .finally(() => {
                    setLoading(false);
                });
        }
    }, [system]);

    const handleDelete = (filename: string) => {
        // Remove the deleted marquee from state
        setMarqueeData(prev => prev.filter(m => m.filename !== filename));
    };

    const filterdData = () => {
        let data = marqueeData?.filter((marquee) => {
            const regex = RegExp(`${debouncedSearchTerm}`, "i");
            return regex.test(marquee.name.toLowerCase().trim());
        });
        if (!data) {
            data = [];
        }
        while (data.length < 10) {
            data.push(emptyMarquee());
        }
        return data;
    };

    return (
        <React.Fragment>
            <Loading isLoading={loading} />
            {!loading && (
                <div className="layout-grid fade-in">
                    <div style={{ display: "flex", gap: "0.5rem", alignItems: "stretch" }}>
                        <input
                            value={searchTerm}
                            onChange={(e) => {
                                setSearchTerm(e.target.value);
                            }}
                            placeholder="Search..."
                            type="text"
                            id="fname"
                            name="fname"
                            style={{
                                minHeight: 54,
                                border: "none",
                                borderRadius: 4,
                                backgroundColor: "antiquewhite",
                                padding: "0 1rem",
                                fontSize: "1.2rem",
                                flex: 1,
                                boxSizing: "border-box",
                            }}
                        />
                        <Button
                            variant="contained"
                            onClick={() => navigate(`/upload-artwork?system=${system}`)}
                            style={{
                                backgroundColor: "#7c3aed",
                                color: "#fff",
                                minWidth: "auto",
                                padding: "0 1rem",
                            }}
                            title="Upload artwork for this system"
                        >
                            <CloudUpload />
                        </Button>
                    </div>
                     {/* <div
                        style={{
                            color: " rgba(107,0,120,1)",
                            backgroundColor: "antiquewhite",
                            padding: ".5rem 0 .5rem 1rem",
                            borderRadius: "4px",
                        }}
                    >
                     <FormControlLabel
                            control={
                                <Switch
                                    checked={streamImages}
                                    onChange={() =>
                                        setStreamImages(!streamImages)
                                    }
                                    color="primary"
                                />
                            }
                            label={<span>Stream Marquees to Panel</span>}
                        /> 
                    </div>*/}
                </div>
            )}

            <div className="layout-grid fade-in">
                {filterdData().map((marquee, index) => {
                    return (
                        <MarqueeItem
                            key={marquee.filename || index}
                            system={system!}
                            marquee={marquee}
                            onDelete={() => handleDelete(marquee.filename)}
                        />
                    );
                })}
            </div>
        </React.Fragment>
    );
};

export default MarqueeList;

