import React, { useState, useEffect } from "react";
import { Button, Switch } from "@material-ui/core";
import { Close, PlayArrow, Cloud } from "@material-ui/icons";
import toast from "react-hot-toast";
import { Select, CaretIcon, ModalCloseButton } from "react-responsive-select";
import { readSettings } from "../../services/settings";
import {
    startWeatherWidget,
    stopWeatherWidget,
    getWeatherStatus,
    updateWeatherSetting,
} from "../../services/weather";
import { dispatchTickerStarted, onTickerStarted } from "../../services/tickerEvents";

type WeatherSettings = {
    weatherLocation: string;
    weatherUnits: string;
    weatherShowUnits: boolean;
    weatherForecast: string;
    weatherRefresh: number;
};

// Refresh presets - values in seconds
const refreshPresets = [
    { text: "5 minutes", value: "300" },
    { text: "15 minutes", value: "900" },
    { text: "30 minutes", value: "1800" },
    { text: "1 hour", value: "3600" },
    { text: "2 hours", value: "7200" },
    { text: "8 hours", value: "28800" },
];

// Forecast mode presets
const forecastPresets = [
    { text: "Today (Hourly)", value: "1day" },
    { text: "3-Day Forecast", value: "3day" },
];

// Unit presets
const unitPresets = [
    { text: "Fahrenheit (F)", value: "fahrenheit" },
    { text: "Celsius (C)", value: "celsius" },
];

interface WeatherWidgetProps {
    onTickerStart?: () => void;
}

const WeatherWidget: React.FC<WeatherWidgetProps> = ({ onTickerStart }) => {
    const [settings, setSettings] = useState<WeatherSettings>({
        weatherLocation: "",
        weatherUnits: "fahrenheit",
        weatherShowUnits: true,
        weatherForecast: "1day",
        weatherRefresh: 1800,
    });
    const [isWidgetRunning, setIsWidgetRunning] = useState(false);
    const [locationInput, setLocationInput] = useState("");
    const [settingsLoaded, setSettingsLoaded] = useState<boolean>(false);
    const [isLoading, setIsLoading] = useState(false);

    // Load settings on mount
    useEffect(() => {
        readSettings()
            .then((data: any) => {
                if (data) {
                    const weatherLocation = data.weatherLocation || "";
                    setSettings({
                        weatherLocation,
                        weatherUnits: data.weatherUnits || "fahrenheit",
                        weatherShowUnits: data.weatherShowUnits !== false,
                        weatherForecast: data.weatherForecast || "1day",
                        weatherRefresh: data.weatherRefresh || 1800,
                    });
                    setLocationInput(weatherLocation);
                    setSettingsLoaded(true);
                }
            })
            .catch(console.error);

        // Check widget status
        getWeatherStatus()
            .then((data: any) => {
                setIsWidgetRunning(data.running || false);
            })
            .catch(() => setIsWidgetRunning(false));

        // Listen for other tickers starting - reset our button state
        const unsubscribe = onTickerStarted((tickerType) => {
            if (tickerType !== 'weather') {
                setIsWidgetRunning(false);
            }
        });

        return () => unsubscribe();
    }, []);

    // Convert seconds to dropdown value string
    const secondsToValueString = (seconds: number): string => {
        const presets = [300, 900, 1800, 3600, 7200, 28800];
        const closest = presets.reduce((prev, curr) =>
            Math.abs(curr - seconds) < Math.abs(prev - seconds) ? curr : prev
        );
        return closest.toString();
    };

    const handleStartWidget = async () => {
        const location = locationInput.trim();
        if (!location) {
            toast.error("Please enter a location (city name or ZIP code)");
            return;
        }

        setIsLoading(true);
        try {
            await startWeatherWidget(
                location,
                settings.weatherUnits,
                settings.weatherShowUnits,
                settings.weatherForecast,
                settings.weatherRefresh
            );
            setIsWidgetRunning(true);
            dispatchTickerStarted('weather');
            onTickerStart?.();

            // Save location
            setSettings({ ...settings, weatherLocation: location });
            toast.success("Weather Widget Started!");
        } catch (error) {
            toast.error("Failed to start weather widget");
        } finally {
            setIsLoading(false);
        }
    };

    const handleStopWidget = async () => {
        setIsLoading(true);
        try {
            await stopWeatherWidget();
            setIsWidgetRunning(false);
            toast.success("Weather Widget Stopped");
        } catch (error) {
            toast.error("Failed to stop weather widget");
        } finally {
            setIsLoading(false);
        }
    };

    const handleSetLocation = () => {
        const location = locationInput.trim();
        if (!location) {
            toast.error("Please enter a location");
            return;
        }

        updateWeatherSetting("weatherLocation", location);
        setSettings({ ...settings, weatherLocation: location });
        toast.success("Location saved!");

        // If widget is running, restart with new location
        if (isWidgetRunning) {
            handleStartWidget();
        }
    };

    const handleUnitsChange = (changes: any) => {
        const units = changes.value;
        const newSettings = { ...settings, weatherUnits: units };
        setSettings(newSettings);
        updateWeatherSetting("weatherUnits", units);

        // If widget is running, restart with new units
        if (isWidgetRunning) {
            startWeatherWidget(
                settings.weatherLocation,
                units,
                settings.weatherShowUnits,
                settings.weatherForecast,
                settings.weatherRefresh
            );
        }
    };

    const handleShowUnitsToggle = () => {
        const newShowUnits = !settings.weatherShowUnits;
        const newSettings = { ...settings, weatherShowUnits: newShowUnits };
        setSettings(newSettings);
        updateWeatherSetting("weatherShowUnits", newShowUnits ? "yes" : "no");

        // If widget is running, restart
        if (isWidgetRunning) {
            startWeatherWidget(
                settings.weatherLocation,
                settings.weatherUnits,
                newShowUnits,
                settings.weatherForecast,
                settings.weatherRefresh
            );
        }
    };

    const handleForecastChange = (changes: any) => {
        const forecast = changes.value;
        const newSettings = { ...settings, weatherForecast: forecast };
        setSettings(newSettings);
        updateWeatherSetting("weatherForecast", forecast);

        // If widget is running, restart with new forecast mode
        if (isWidgetRunning) {
            startWeatherWidget(
                settings.weatherLocation,
                settings.weatherUnits,
                settings.weatherShowUnits,
                forecast,
                settings.weatherRefresh
            );
        }
    };

    const handleRefreshChange = (changes: any) => {
        const refreshSeconds = parseInt(changes.value, 10);
        const newSettings = { ...settings, weatherRefresh: refreshSeconds };
        setSettings(newSettings);
        updateWeatherSetting("weatherRefresh", refreshSeconds.toString());

        // If widget is running, restart with new refresh
        if (isWidgetRunning) {
            startWeatherWidget(
                settings.weatherLocation,
                settings.weatherUnits,
                settings.weatherShowUnits,
                settings.weatherForecast,
                refreshSeconds
            );
        }
    };

    return (
        <div className="widget-section">
            <h3 className="section-title">
                <Cloud style={{ marginRight: 8, verticalAlign: "middle" }} />
                Weather Widget
            </h3>

            {/* All settings on one row */}
            <div className="settings-grid">
                {/* Location Input */}
                <div className="setting-item">
                    <label className="setting-label">
                        {settings.weatherLocation
                            ? `Location: ${settings.weatherLocation}`
                            : "Location"}
                    </label>
                    <input
                        type="text"
                        className="symbol-input"
                        placeholder="ZIP or city"
                        value={locationInput}
                        onChange={(e) => setLocationInput(e.target.value)}
                        onKeyPress={(e) => {
                            if (e.key === "Enter") {
                                handleSetLocation();
                            }
                        }}
                    />
                </div>

                {/* Temperature Unit */}
                <div className="setting-item">
                    <label className="setting-label">Unit</label>
                    <Select
                        key={`units-select-${settingsLoaded}`}
                        selectedValue={settings.weatherUnits}
                        name="temperatureUnits"
                        options={unitPresets}
                        caretIcon={<CaretIcon />}
                        modalCloseButton={<ModalCloseButton />}
                        onChange={handleUnitsChange}
                    />
                </div>

                {/* Display Units Toggle */}
                <div className="setting-item">
                    <label className="setting-label">Show F/C</label>
                    <Switch
                        checked={settings.weatherShowUnits}
                        onChange={handleShowUnitsToggle}
                        color="primary"
                    />
                </div>

                {/* Forecast Mode */}
                <div className="setting-item">
                    <label className="setting-label">Forecast</label>
                    <Select
                        key={`forecast-select-${settingsLoaded}`}
                        selectedValue={settings.weatherForecast}
                        name="forecastMode"
                        options={forecastPresets}
                        caretIcon={<CaretIcon />}
                        modalCloseButton={<ModalCloseButton />}
                        onChange={handleForecastChange}
                    />
                </div>

                {/* Refresh Interval */}
                <div className="setting-item">
                    <label className="setting-label">Refresh</label>
                    <Select
                        key={`refresh-select-${settingsLoaded}-${settings.weatherRefresh}`}
                        selectedValue={secondsToValueString(settings.weatherRefresh)}
                        name="refreshInterval"
                        options={refreshPresets}
                        caretIcon={<CaretIcon />}
                        modalCloseButton={<ModalCloseButton />}
                        onChange={handleRefreshChange}
                    />
                </div>
            </div>

            {/* Start/Stop Button */}
            <Button
                variant="contained"
                className={`start-button ${isWidgetRunning ? "stop" : ""}`}
                onClick={isWidgetRunning ? handleStopWidget : handleStartWidget}
                disabled={isLoading || (!isWidgetRunning && !locationInput.trim())}
            >
                {isWidgetRunning ? (
                    <Close />
                ) : (
                    <PlayArrow />
                )}
                {isLoading
                    ? "PLEASE WAIT..."
                    : isWidgetRunning
                    ? "STOP WEATHER"
                    : "START WEATHER"}
            </Button>
        </div>
    );
};

export default WeatherWidget;
