import axios from "axios";
import { getDeviceUrl } from ".";

// Start the stock ticker
export const startStockTicker = (
    symbols: string[],
    speed: number = 3,
    fps: number = 60,
    refresh: number = 5,
    color: string = "FFFFFF",
    font: string = "LiberationSans-Bold"
): Promise<any> => {
    const symbolsParam = symbols.join(",");
    const requestUrl = `${getDeviceUrl("")}/stocks/start?symbols=${encodeURIComponent(symbolsParam)}&speed=${speed}&fps=${fps}&refresh=${refresh}&color=${color}&font=${encodeURIComponent(font)}`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Stop the stock ticker
export const stopStockTicker = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/stocks/stop`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Get stock ticker status
export const getStockTickerStatus = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/stocks/status`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Get stock quotes without starting the ticker
export const getStockQuotes = (symbols: string[]): Promise<any> => {
    const symbolsParam = symbols.join(",");
    const requestUrl = `${getDeviceUrl("")}/stocks/quotes?symbols=${encodeURIComponent(symbolsParam)}`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Update a stock ticker setting
export const updateStockSetting = (key: string, value: string): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/settings?key=${key}&value=${encodeURIComponent(value)}`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};
