import axios from "axios";
import { getDeviceUrl } from ".";

// Start the news ticker
export const startNewsTicker = (
    sources: string, // comma-separated list of source keys
    customURL: string = "",
    speed: number = 5,
    fps: number = 60,
    refresh: number = 5,
    color: string = "FFFFFF",
    font: string = "LiberationSans-Regular"
): Promise<any> => {
    let requestUrl = `${getDeviceUrl("")}/news/start?sources=${encodeURIComponent(sources)}&speed=${speed}&fps=${fps}&refresh=${refresh}&color=${color}&font=${encodeURIComponent(font)}`;

    if (sources.includes("custom") && customURL) {
        requestUrl += `&customURL=${encodeURIComponent(customURL)}`;
    }

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Stop the news ticker
export const stopNewsTicker = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/news/stop`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Get news ticker status
export const getNewsTickerStatus = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/news/status`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Get available news sources
export const getNewsSources = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/news/sources`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Fetch news without starting the ticker
export const fetchNews = (source: string, customURL: string = ""): Promise<any> => {
    let requestUrl = `${getDeviceUrl("")}/news/fetch?source=${encodeURIComponent(source)}`;

    if (source === "custom" && customURL) {
        requestUrl += `&customURL=${encodeURIComponent(customURL)}`;
    }

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

// Update a news ticker setting
export const updateNewsSetting = (key: string, value: string): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/settings?key=${key}&value=${encodeURIComponent(value)}`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};
