// Ticker event system to coordinate button states between widgets

export type TickerType = 'sports' | 'stocks' | 'weather' | 'news' | 'clock';

// Custom event for when a ticker starts (which stops others)
export const TICKER_STARTED_EVENT = 'tickerStarted';

// Dispatch event when a ticker starts
export const dispatchTickerStarted = (tickerType: TickerType) => {
    window.dispatchEvent(new CustomEvent(TICKER_STARTED_EVENT, {
        detail: { tickerType }
    }));
};

// Subscribe to ticker started events
export const onTickerStarted = (
    callback: (tickerType: TickerType) => void
): (() => void) => {
    const handler = (event: Event) => {
        const customEvent = event as CustomEvent<{ tickerType: TickerType }>;
        callback(customEvent.detail.tickerType);
    };
    window.addEventListener(TICKER_STARTED_EVENT, handler);
    // Return unsubscribe function
    return () => window.removeEventListener(TICKER_STARTED_EVENT, handler);
};
